import Decimal from 'decimal.js';

const $Tool = {
  $Number: function(a) {
    return new Decimal(a).toNumber();
  },
  $add: function(a, b) {
    return new Decimal(Number(a)).add(new Decimal(Number(b))).toNumber();
  },
  $sub: function(a, b) {
    return new Decimal(a).sub(new Decimal(b)).toNumber();
  },
  $mul: function(a, b) {
    return new Decimal(a).mul(new Decimal(b)).toNumber();
  },
  $div: function(a, b) {
    return new Decimal(a).div(new Decimal(b)).toNumber();
  },
  $ceil: function(val,digit = 2) {
    /**
     * @author DDB
     * @method 对给定数值进行给定位数的，进位舍去小数位
     * @Description
     * @param {string} val 数值
     * @param {string} digit 位数
     * @return {string} 进位舍弃位数后的结果
    */
    return new Decimal(Math.ceil(val * Math.pow(10,digit))).div(new Decimal(Math.pow(10,digit))).toNumber();
  },
  $floor: function(val,digit = 2) {
    return new Decimal(Math.floor(val * Math.pow(10,digit))).div(new Decimal(Math.pow(10,digit))).toNumber();
  },
  deepCopy(obj) {
    return JSON.parse(JSON.stringify(obj));
  },
  browserType() {
    let u = global.navigator.userAgent;
    return {
      wechat: u.toLowerCase().indexOf('micromessenger') != -1,
      trident: u.indexOf('Trident') > -1, //IE内核
      presto: u.indexOf('Presto') > -1, //Opera内核
      webKit: u.indexOf('AppleWebKit') > -1, //苹果/谷歌内核
      gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1, //火狐内核
      // ||!!u.match(/AppleWebKit/)&&u.indexOf('QIHU')&&u.indexOf('Chrome')<0
      mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
      ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //iOS终端
      android: /Android/i.test(u), //Android终端或者UC浏览器
      iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //是否为iPhone或者QQHD浏览器
      iPad: u.indexOf('iPad') > -1, //是否iPad
      webApp: u.indexOf('Safari') === -1, //是否Web应该程序，没有头部与底部
    }
  },
  getQueryString(name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
    var r = window.location.search.substr(1).match(reg);
    if (r != null) {
      return unescape(r[2]);
    }
    return null;
  },
  checkMobile(s) {
    let r = /^1[34578]\d{9}$/;
    return r.test(s);
  },
  checkCode(s) {
    let r = /^[0-9]{4,6}$/;
    return r.test(s);
  },
  checkPassword(s) {
    let r = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/;
    return r.test(s);
  },
  prompt(context) {
    if (document.getElementById('Toast')) {
      document.body.removeChild(document.getElementById('Toast'));
    }
    let Span = document.createElement('span');
    Span.innerHTML = `<span>${context}</span>`;
    Span.id = 'Toast';
    Span.setAttribute('class', '_alert');
    document.body.appendChild(Span);

    if (this.$ToastSetTimeout) {
      clearTimeout(this.$ToastSetTimeout);
      this.$ToastSetTimeout = null;
    }
    this.$ToastSetTimeout = setTimeout(() => {
      if (document.getElementById('Toast')) {
        document.getElementById('Toast').parentNode.removeChild(document.getElementById('Toast'));
      }
    }, 3000);
  },
};

/**
 * @author DDB
 * @method DragElement
 * @Description 用于元素拖拽的类
 * @param {Object} el 参数：需要拖拽的dom元素
 * @return {Object}
*/
class DragElement {
  constructor(el) {
    this.el = el;
    this.disX = 0;
    this.disY = 0;
    this.init();
  }
  init() {
    if ($Tool.browserType().mobile) {
        // this.el.ontouchstart = (e) => {
        //   this.disX = e.touches[0].clientX - this.el.offsetLeft;
        //   this.disY = e.touches[0].clientY - this.el.offsetTop;
        //   document.ontouchmove = this.mobileMove.bind(this);
        //   document.ontouchend = this.mobileEnd.bind(this);
        // };
      this.el.ontouchstart = this.mobileStart.bind(this);
    }else {
      // this.el.onmousedown = (e) => {
      //   this.disX = e.clientX - this.el.offsetLeft;
      //   this.disY = e.clientY - this.el.offsetTop;
      //   document.onmousemove = this.pcMove.bind(this);
      //   document.onmouseup = this.pcUp.bind(this);
      // }
      this.el.onmousedown = this.pcDown.bind(this);
    }
  }
  pcDown(e) {
    this.disX = e.clientX - this.el.offsetLeft;
    this.disY = e.clientY - this.el.offsetTop;
    document.onmousemove = this.pcMove.bind(this);
    document.onmouseup = this.pcUp.bind(this);
  }
  pcMove(e) {
    this.el.style.left = e.clientX - this.disX + 'px';
    this.el.style.top = e.clientY - this.disY + 'px';
  }
  pcUp() {
    document.onmousemove = null;
    document.onmouseup = null
  }
  mobileStart(e) {
    this.disX = e.touches[0].clientX - this.el.offsetLeft;
    this.disY = e.touches[0].clientY - this.el.offsetTop;
    document.ontouchmove = this.mobileMove.bind(this);
    document.ontouchend = this.mobileEnd.bind(this);
  }
  mobileMove(e) {
    this.el.style.left = e.touches[0].clientX - this.disX + 'px';
    this.el.style.top = e.touches[0].clientY - this.disY + 'px';
  }
  mobileEnd() {
    document.ontouchmove = null;
    document.ontouchend = null;
  }
}

export default $Tool
export {DragElement}



