<template>
    <div class="menuPage" id="menuPage">
        <div class="close" @click="close"></div>
        <ul class="menuList">
            <li v-for="(item,index) in currentMenuList" :key="index" :class="{'act':menuCurrentPage === item.type}" @click="toNextPage(item.type)">{{$t('Menu["'+item.type+'"]')}}</li>
        </ul>
    </div>
</template>

<script>
  export default {
    name: 'Menu',
    data() {
      return {
        currentMenuList: [],
        notLoginMenuList: [
          {
            type: 'Home'
          },
          {
            type: 'Calculate'
          },
          {
            type: 'Personal'
          },
          {
            type: 'SignIn'
          },
          {
            type: 'SignUp'
          },
          {
            type: 'Setting'
          }
        ],
        loginMenuList: [
          {
            type: 'Home'
          },
          {
            type: 'Calculate'
          },
          {
            type: 'Personal'
          },
          {
            type: 'Setting'
          },
          {
            type: 'Exit'
          }
        ],
        isLogin: false,
      }
    },
    methods: {
      toNextPage(pageType) {
        if (this.menuCurrentPage !== pageType) {
          this.menuCurrentPage = pageType;
          this.$router.push({
            path: pageType === 'Home' ? '/' : '/'+pageType,
          })
        }
        this.close();
      },
      getMenuList() {
        this.currentMenuList = this.isLogin ? [...this.loginMenuList] : [...this.notLoginMenuList];
      },
      close() {
        this.$emit('closeMenu');
      }
    },
    mounted() {
      console.log('打开菜单');
      this.menuCurrentPage = this.currentPage;
      this.getMenuList();
    },
    props: {
      currentPage: {
        type: String
      }
    }
  };
</script>

<style lang="less" scoped>
    #menuPage {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(22,22,22,1);
        z-index: 99;
        .close {
            position: absolute;
            top: 1rem;
            /*top: 0.5rem;*/
            right: 0.5rem;
            width: 0.3rem;
            height: 0.3rem;
            background: url("../assets/img/menu_close.png") no-repeat center center;
            background-size: cover;
        }
        .menuList {
            margin-top: 2.95rem;
            & > li {
                font-size: 0.34rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color: rgba(174,174,174,1);
                text-align: center;
                margin-bottom: 0.8rem;
            }
            .act {
                color: #3D6CFF;
            }
        }
    }
</style>


