<template>
    <div class="layer" @touchmove.prevent>
        <div class="promptC">
            <div class="head">优惠卷</div>
            <div class="content">
                <ul class="couponsList">
                    <li v-for="(item,index) in couponsList" :key="index">
                        <span class="title">优惠：</span>
                        <span class="couponPrice">$ {{item.value}}</span>
                        <input class="couponId" type="radio" name="couponId">
                    </li>
                </ul>
            </div>
            <div class="footer">
                <button class="left" @click="handleLeftEvent">取消</button>
                <button class="right act" @click="handleRightEvent">确定</button>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'CouponsPrompt',
    data() {
      return {
        // 中英文 采用方法重置
        couponsList: [
          {
            limitProduct: ['01','03'], // 可以使用的产品id, 为空则表示没有限制
            range: 50, // 使用范围，表示产品总价超过50才能用
            value: 10  // 优惠数额，10元优惠券（在指定产品满50可用）
          },
          {
            limitProduct: ['01','03'],
            range: 50,
            value: 10
          },
          {
            limitProduct: ['01','03'], // 可以使用的产品id, 为空则表示没有限制
            range: 50, // 使用范围，表示产品总价超过50才能用
            value: 10  // 优惠数额，10元优惠券（在指定产品满50可用）
          }
        ]
      }
    },
    methods: {
      handleLeftEvent() {

      },
      handleRightEvent() {

      }
    },
    mounted() {

    }
  };
</script>

<style lang="less" scoped>
    .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 99;
        .promptC {
            margin: 0 auto;
            width: 5.70rem;
            height: 7.3rem;
            background: rgba(255,255,255,1);
            border-radius: 0.20rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .head {
                margin: 0 auto;
                width: 5.2rem;
                height: 1.4rem;
                font-size: 0.40rem;
                font-family: PingFangSC-Semibold;
                font-weight: bold;
                color: rgba(32,47,66,1);
                display: flex;
                align-items: center;
                justify-content: center;
                border-bottom: 1px solid rgba(242, 242, 242, 1);
            }
            .content {
                margin: 0 auto;
                width: 5.2rem;
                height: 4.5rem;
                text-align: center;
                font-size: 0.32rem;
                color: rgba(0,0,0,1);
                /*background-color: cyan;*/
                .couponsList {
                    & > li {
                        height: 1.0rem;
                        display: flex;
                        align-items: center;
                        border-bottom: 1px solid rgba(242, 242, 242, 1);
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(111,111,111,1);
                        .title {
                            margin-left: 0.15rem;
                        }
                        .couponPrice {
                            margin-left: 0.1rem;
                        }
                        input[type='radio'] {
                            margin-left: auto;
                            margin-right: 0.15rem;
                            width: 0.4rem;
                        }
                    }
                }

            }
            .footer {
                margin-top: 0.3rem;
                width: 100%;
                height: 1.0rem;
                border-top: 1px solid rgba(216,216,216,1);
                display: flex;
                align-items: center;
                justify-content: center;
                & > button {
                    width: 50%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 0.34rem;
                    color: rgba(0,0,0,1);
                    background-color: inherit;
                }
                & > button:first-child {
                    border-right: 1px solid rgba(216,216,216,1);
                }
                & > button.act {
                    color: rgba(61, 121, 255, 1);
                }

            }
        }

    }
</style>


