  import $Tool from '../../api/util';

  export default {
    name: 'Home',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
        },
        bannerTimer: null,
        bannerList: ['3','1','2','3','1'],
        bannerIndex: 1,
        lastBannerIndex: 1,
        windTalkCoinInfo: {
          dollarToCoinRate: 10000 // 单位美元等于多少金币
        },
        virtualProductList: [
          {
            id: '01', // 产品id
            deadline: 7, // 期限
            yieldInDay: 0.3834,  // 理论每天每T收益
            yieldInYear: 179.03,  // 理论年化收益率
            calculateInDay: 0.1329, // 每天每T租赁费
            electricInDay: 0.1249, // 每天每T电费
            calculateStepNum: 1, // 算力步进值
            minPowerNum: 1,     // 购买算力下限
            maxPowerNum: 100,  // 购买算力上限
            digStartTimes: '19.8.30 08:00',  // 挖矿预计开始时间
            digCoinType: 'Bitcoin', // 挖取币种
            oreMachineType: 'Antminer S9 (6)', // 机型
            maxDeductionRate: 1,// 最大抵扣比率
            soldRate: 0.5, // 已出售比率
            // 计算获取
            electricDays: 7, // 电费缴纳天数
            powerNum: 1, // 购买算力数量
            powerToPrice: 0, // 购买算力所需美元
            electricToPrice: 0,  // 购买电费所需美元
            totalPrice: 0 // 总费用
          }
        ],
        realProductList: [
          {
            id: '', // 产品id
            deadline: 120, // 期限
            paybackPeriod: 170, //理论回本时间
            yieldInDay: 0.3834,  // 理论每天每T收益
            yieldInYear: 179.03,  // 理论年化收益率
            calculateInDay: 0.1329, // 每天每T租赁费
            calculateStepNum: 1, // 算力步进值
            electricInDay: 0.1249, // 每天每T电费
            electricMinDays: 10, // 电费天数最小值，也是默认值
            electricStepNum: 10, // 电费步进值
            minPowerNum: 1,     // 购买算力下限
            maxPowerNum: 100,  // 购买算力上限
            digStartTimes: '',  // 挖矿预计开始时间
            digCoinType: 'Bitcoin', // 挖取币种
            oreMachineType: 'Antminer S9 (6)', // 机型
            soldRate: 0.5, // 已出售比率
            // 计算获取
            electricDays: 0, // 电费缴纳天数
            powerNum: 1, // 购买算力数量
            powerToPrice: 0, // 购买算力所需美元
            electricToPrice: 0,  // 购买电费所需美元
            totalPrice: 0 // 总费用
          },
          {
            id: '', // 产品id
            deadline: 120, // 期限
            paybackPeriod: 170,
            yieldInDay: 0.3834,  // 理论每天每T收益
            yieldInYear: 179.03,  // 理论年化收益率
            calculateInDay: 0.1329, // 每天每T租赁费
            calculateStepNum: 1, // 算力步进值
            electricInDay: 0.1249, // 每天每T电费
            electricMinDays: 10, // 电费天数最小值，也是默认值
            electricStepNum: 10, // 电费步进值
            minPowerNum: 0,     // 购买算力下限
            maxPowerNum: 0,  // 购买算力上限
            digStartTimes: '19.8.30 08:00',  // 挖矿预计开始时间
            digCoinType: 'Bitcoin', // 挖取币种
            oreMachineType: 'Antminer S9 (6)', // 机型
            soldRate: 1, // 已出售比率
            // 计算获取
            electricDays: 0, // 电费缴纳天数
            powerNum: 1, // 购买算力数量
            powerToPrice: 0, // 购买算力所需美元
            electricToPrice: 0,  // 购买电费所需美元
            totalPrice: 0 // 总费用
          }
        ],
        couponsList: [
          {
            limitProduct: ['01','03'], // 可以使用的产品id, 为空则表示没有限制
            range: 50, // 使用范围，表示产品总价超过50才能用
            value: 10  // 优惠数额，10元优惠券（在指定产品满50可用）
          }
        ]
      }
    },
    methods: {
      // inputFocus(event) { event.currentTarget.select(); },
      subPowerNum(item) {
        if (item.powerNum <= item.minPowerNum) return false;
        item.powerNum = $Tool.$sub(item.powerNum,item.calculateStepNum);
      },
      addPowerNum(item) {
        if (item.powerNum >= item.maxPowerNum) return false;
        item.powerNum = $Tool.$add(item.powerNum,item.calculateStepNum);
      },
      toOrderPage(type,item) {
        if (item.soldRate >= 1) {return false;}
        localStorage.setItem(`current${type}Item`,JSON.stringify(item));
        this.$router.push({
          path:'/'+type+'Order'
        })
      },
      toCalculatePage() {
        this.$router.push({
          path:'/Calculate'
        })
      },
      getExchangeRate() {
        this.$ajax('/windmine-product/exchangerate', {}, res => {
          console.log('res', res.data);
          if (res.data.success == true) {
            this.windTalkCoinInfo.dollarToCoinRate = res.data.exchange_value;
          }
        }, 'get');
      },
      getVirtualProductList() {
        // this.$ajax('/windmine-user/product/coinproduct', {}, res => {
        //   console.log('res', res.data);
        //   if (res.data.success == true) {
        //     this.virtualProductList = [...res.data.data.virtualProductList];
        //   }
        // }, 'get');

        // 重置总计费用
        this.virtualProductList.forEach((item, index, array) => {
          item.powerNum = item.minPowerNum;
          item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline,$Tool.$mul(item.calculateInDay,item.powerNum||0)));
          item.electricToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.electricInDay,item.powerNum||0)));
          item.totalPrice = $Tool.$add(item.powerToPrice,item.electricToPrice);
          // console.log(item.powerToPrice,item.electricToPrice);
          // console.log('item.totalPrice',item.totalPrice);
        })
      },
      getRealProductList() {
        // this.$ajax('/windmine-user/product/calculateroduct', {}, res => {
        //   console.log('res', res.data);
        //   if (res.data.success == true) {
        //     this.realProductList = [...res.data.data.realProductList];
        //   }
        // }, 'get');

        // 重置算力租赁费
        this.realProductList.forEach(((item, index, array) => {
          item.powerNum = item.minPowerNum;
          item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.calculateInDay,item.powerNum||0)));
        }))
      },
      bannerHandler(e) {
        this.bannerLoop();
        this.bannerDrag();
      },
      bannerDrag() {
        let that = this;
        let $bannerList = that.$refs.bannerList;
        let baseWidth = that.$refs.bannerView.offsetWidth;
        console.log('$bannerList', baseWidth);
        class DragXElement extends DragElement {
          constructor(...arg) {
            super(...arg);
            console.log(this.el.offsetLeft);
            this.currentDistance = -baseWidth;
            this.el.style.left = -baseWidth + 'px';
          }
          mobileStart(e) {
            console.log(this.el.offsetLeft);
            that.clearBannerTimer();
            this.currentDistance = this.el.offsetLeft;
            super.mobileStart(e);
          }
          mobileMove(e) {
            this.el.style.left = e.touches[0].clientX - this.disX + 'px';
          }
          mobileEnd(e) {

            console.log('x2',this.el.offsetLeft);
            console.log('x1',$Tool.$sub(this.currentDistance,baseWidth/2));

            if (this.el.offsetLeft >= $Tool.$add(this.currentDistance,baseWidth/2)) {
              that.bannerIndex--;
              if (that.bannerIndex <= 0) {
                that.bannerIndex = that.bannerList.length - 2;
              }
            }else if(this.el.offsetLeft <= $Tool.$sub(this.currentDistance,baseWidth/2)){
              that.bannerIndex++;
              if (that.bannerIndex >= that.bannerList.length - 1) {
                that.bannerIndex = 1;
              }
            }
            this.el.style.left = $Tool.$mul(that.bannerIndex,-baseWidth) + 'px';
            console.log('that.bannerIndex',that.bannerIndex);
            that.bannerLoop();
            super.mobileEnd(e);
          }
        }
        let dragObj = new DragXElement($bannerList);
      },
      bannerLoop() {
        let $bannerList = this.$refs.bannerList;
        let baseWidth = this.$refs.bannerView.offsetWidth;
        this.clearBannerTimer();
        this.bannerTimer = setInterval(()=>{
            this.lastBannerIndex = this.bannerIndex;
            this.bannerIndex++;
            if (this.bannerIndex > this.bannerList.length - 2) {
              this.bannerIndex = 1;
            }
          $bannerList.style.left = $Tool.$mul(this.bannerIndex,-baseWidth) + 'px';
        },3000)
      },
      clearBannerTimer() {
        if (this.bannerTimer) {
          clearInterval(this.bannerTimer);
          this.bannerTimer = null;
        }
      },
      popstate() {
        // TODO: 只监听出栈
        window.addEventListener('popstate', (e)=>{
          console.log('e',e);
        });
      }
    },
    mounted() {
      console.log('进入home');
      this.getVirtualProductList();
      this.getRealProductList();
      this.bannerHandler();
      // TODO: 测试路由
      // this.popstate();
      // console.log('this.$i18n.locale',this.$i18n.locale);
      // debugger;
    },
    watch: {
      // TODO: 每次改变，会循环两次。优化可以考虑镜像数组
      virtualProductList: {
        handler: function (arr, oldArr) {
          arr.forEach(item => {
            if (!item.powerNum || item.powerNum < item.minPowerNum) {
              item.powerNum = item.minPowerNum;
            }
            if (item.powerNum > item.maxPowerNum) {
              item.powerNum = item.maxPowerNum;
            }
            // 公式: 合约天数 * (单位每天每T算力费 * 购买T数 + 每天电费)
            // 公式: 总算力费 + 总电费 ==>  合约天数 * (单位每天每T算力费 * 购买T数)  + 合约天数 * 每天电费
            item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline,$Tool.$mul(item.calculateInDay,item.powerNum||0)));
            item.electricToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.electricInDay,item.powerNum||0)));
            item.totalPrice = $Tool.$add(item.powerToPrice,item.electricToPrice);
            // console.log(item.powerToPrice,item.electricToPrice);

          })
        },
        deep: true
      },
      realProductList: {
        handler: function (arr, oldArr) {
          arr.forEach(item => {
            if (!item.powerNum || item.powerNum < item.minPowerNum) {
              item.powerNum = item.minPowerNum;
            }
            if (item.powerNum > item.maxPowerNum) {
              item.powerNum = item.maxPowerNum;
            }
            // 公式: 合约天数 * (单位每天每T算力费 * 购买T数)
            item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.calculateInDay,item.powerNum||0)));
          })
        },
        deep: true
      }
    },
    components: {

    }
  };
</script>

