import Vue from 'vue'
import Router from 'vue-router'
import $Tool from '../api/util'
import Config from '../config'
import i18n from '../language/'

// TODO: 页面导入是否根据条件导入 待
// TODO:  H5移动端
import Home from '../pages/H5/Home'
import Calculate from '../pages/H5/Calculate'
import Personal from '../pages/H5/Personal'
import Setting from '../pages/H5/Setting'
import SignIn from '../pages/H5/SignIn'
import SignUp from '../pages/H5/SignUp'
import Forget from '../pages/H5/ForgetPassword'
import CalculateOrder from '../pages/H5/CalculateOrder'
import CoinOrder from '../pages/H5/CoinOrder'
import Payment from '../pages/H5/Payment'
// TODO: H5移动端 --PersonalPages--二级页面
import AssetManage from '../pages/H5/PersonalPages/AssetManage'
import InviteReward from '../pages/H5/PersonalPages/InviteReward'
import MyOrder from '../pages/H5/PersonalPages/MyOrder'
import Coupons from '../pages/H5/PersonalPages/Coupons'
// TODO: H5移动端 --PersonalPages--AssetManage--三级页面
import Withdraw from '../pages/H5/PersonalPages/AssetManagePages/Withdraw'
import TakeCoin from '../pages/H5/PersonalPages/AssetManagePages/TakeCoin'
import TradingRecord from '../pages/H5/PersonalPages/AssetManagePages/TradingRecord'

// TODO: H5移动端 --SettingPages--二级页面
import Address from '../pages/H5/SettingPages/Address'
import Remind from '../pages/H5/SettingPages/Remind'
import Safe from '../pages/H5/SettingPages/Safe'


// TODO: PC端
import PCHome from '../pages/PC/PCHome'

// TODO: Andriod 内嵌端
import AHome from '../pages/Android/AHome'
import AAssetManage from '../pages/Android/AAssetManage'
import AMyOrder from '../pages/Android/AMyOrder'
import AInviteReward from '../pages/Android/AInviteReward'
import ASetting from '../pages/Android/ASetting'

import ACalculateProduct from '../pages/Android/ACalculateProduct'
import ACoinProduct from '../pages/Android/ACoinProduct'
import ACalculateOrder from '../pages/Android/ACalculateOrder'
import ACoinOrder from '../pages/Android/ACoinOrder'
import APayment from '../pages/Android/APayment'

import ATradingRecord from '../pages/Android/AssetManagePages/TradingRecord'
import AWithdraw from '../pages/Android/AssetManagePages/Withdraw'
import ATakeCoin from '../pages/Android/AssetManagePages/TakeCoin'

let tempVue = new Vue({i18n});

// TODO: 配置
/*
* 配置说明：
* 打包：
*   H5与PC公用一套
*   安卓内嵌一套
* 表现：
*   H5、PC访问根路径相同，子路径不相同，若子路径与终端不匹配，跳转匹配终端的根路径。
*
* */
let _routes = [];
let _rootPathName = ''; // 初始路径
let _rootComponent = null;
let browser = $Tool.browserType();
if (Config.terminalMode !== 'Android') {
  _rootPathName = browser.mobile ? 'Home' : 'PCHome';
  _rootComponent = browser.mobile ? Home : PCHome;
  _routes = [
    {
      path: '/',
      name: _rootPathName,
      component: _rootComponent,
    },
    // TODO:H5****************************************************************************************************
    {
      path: '/Calculate',
      name: 'Calculate',
      component: Calculate,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/Personal',
      name: 'Personal',
      component: Personal,
      meta: {
        type: 'mobile'
      },
      children: [
        {
          path: 'AssetManage',
          name: 'AssetManage',
          component: AssetManage,
          meta: {
            type: 'mobile'
          }
        },
        {
          path: 'InviteReward',
          component: InviteReward,
          meta: {
            type: 'mobile'
          }
        },
        {
          path: 'MyOrder',
          component: MyOrder,
          meta: {
            type: 'mobile'
          }
        },
        {
          path: 'Coupons',
          component: Coupons,
          meta: {
            type: 'mobile'
          }
        }
      ]
    },
    {
      path: '/Setting',
      name: 'Setting',
      component: Setting,
      meta: {
        type: 'mobile'
      },
      children: [
        {
          path: 'Address',
          component: Address,
          meta: {
            type: 'mobile'
          }
        },
        {
          path: 'Remind',
          component: Remind,
          meta: {
            type: 'mobile'
          }
        },
        {
          path: 'Safe',
          component: Safe,
          meta: {
            type: 'mobile'
          }
        }
      ]
    },
    {
      path: '/SignIn',
      name: 'SignIn',
      component: SignIn,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/SignUp',
      name: 'SignUp',
      component: SignUp,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/Forget',
      name: 'Forget',
      component: Forget,
      meta: {
        type: 'mobile'
      }
    },
    // TODO: 二级页面，占用一级路由
    {
      path: '/CalculateOrder',
      name: 'CalculateOrder',
      component: CalculateOrder,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/CoinOrder',
      name: 'CoinOrder',
      component: CoinOrder,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/Payment',
      name: 'Payment',
      component: Payment,
      meta: {
        type: 'mobile'
      }
    },
    // TODO: 三级页面，占用一级路由
    {
      path: '/Personal/AssetManage/Withdraw',
      name: 'Withdraw',
      component: Withdraw,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/Personal/AssetManage/TakeCoin',
      name: 'TakeCoin',
      component: TakeCoin,
      meta: {
        type: 'mobile'
      }
    },
    {
      path: '/Personal/AssetManage/TradingRecord',
      name: 'TradingRecord',
      component: TradingRecord,
      meta: {
        type: 'mobile'
      }
    }
    // TODO:Web****************************************************************************************************
  ]
}else {
  _rootPathName = 'AHome';
  _rootComponent = AHome;
  _routes = [
    {
      path: '/',
      name: _rootPathName,
      component: _rootComponent,
      meta: {
        title: tempVue.$t('Meta.title.android.home')
      }
    },
    // TODO:安卓****************************************************************************************************
    {
      path: '/AAssetManage',
      name: 'AAssetManage',
      component: AAssetManage,
      meta: {
        title: tempVue.$t('Meta.title.android.assetManage')
      }
    },
    {
      path: '/AMyOrder',
      name: 'AMyOrder',
      component: AMyOrder,
      meta: {
        title: tempVue.$t('Meta.title.android.myOrder')
      }
    },
    {
      path: '/AInviteReward',
      name: 'AInviteReward',
      component: AInviteReward,
      meta: {
        title: tempVue.$t('Meta.title.android.inviteReward')
      }
    },
    {
      path: '/ASetting',
      name: 'ASetting',
      component: ASetting,
      meta: {
        title: tempVue.$t('Meta.title.android.setting')
      }
    },
    {
      path: '/ACalculateProduct',
      name: 'ACalculateProduct',
      component: ACalculateProduct,
      meta: {
        title: tempVue.$t('Meta.title.android.calculateProduct')
      }
    },
    {
      path: '/ACoinProduct',
      name: 'ACoinProduct',
      component: ACoinProduct,
      meta: {
        title: tempVue.$t('Meta.title.android.coinProduct')
      }
    },
    {
      path: '/ACalculateOrder',
      name: 'ACalculateOrder',
      component: ACalculateOrder,
      meta: {
        title: tempVue.$t('Meta.title.android.calculateOrder')
      }
    },
    {
      path: '/ACoinOrder',
      name: 'ACoinOrder',
      component: ACoinOrder,
      meta: {
        title: tempVue.$t('Meta.title.android.coinOrder')
      }
    },
    {
      path: '/APayment',
      name: 'APayment',
      component: APayment,
      meta: {
        title: tempVue.$t('Meta.title.android.payment')
      }
    },
    // TODO: 二级页面，占用一级路由
    {
      path: '/AssetManage/AWithdraw',
      name: 'AWithdraw',
      component: AWithdraw,
      meta: {
        title: tempVue.$t('Meta.title.android.withdraw')
      }
    },
    {
      path: '/AssetManage/ATakeCoin',
      name: 'ATakeCoin',
      component: ATakeCoin,
      meta: {
        title: tempVue.$t('Meta.title.android.takeCoin')
      }
    },
    {
      path: '/AssetManage/ATradingRecord',
      name: 'ATradingRecord',
      component: ATradingRecord,
      meta: {
        title: tempVue.$t('Meta.title.android.tradingRecord')
      }
    }
  ]
}


Vue.use(Router);

const router = new Router({
  // mode: 'history',
  routes: _routes
});

router.beforeEach((to, from, next) => {
  // ...
  console.log('from:',from);
  console.log('to:',to);
  document.title = to.meta.title || tempVue.$t('title');

  // TODO: 增强判断
  if (!from.name) {
    if (to.path === '/') {
      console.log('到首页');
      next();
    }else if ((to.meta.type === 'mobile' && browser.mobile) || (to.meta.type === 'PC' && !browser.mobile)) {
      next();
    }else {
      next({path: '/'});
    }
  }else {
    next();
  }
});


export default router



