<template>
    <div class="addressPage">
        <div class="handleC">
            <div class="title">{{$t('Setting.address.title')}}</div>
            <div class="addAddress" @click="addAddress">
                <span>+</span>
                <span>{{$t('Setting.address.add')}}</span>
            </div>
        </div>
        <div class="addressListC">
            <div class="head">BTC</div>
            <ul class="addressList">
                <li v-for="(item,index) in addressList" :key="index">
                    <div class="addressMsg">
                        <div class="remark">{{item.remark}}</div>
                        <div class="address">{{item.address}}</div>
                    </div>
                    <div class="update" @click="updateAddress(item,index)">{{$t('Setting.address.update')}}</div>
                    <div class="delete" @click="deleteAddress(item,index)">{{$t('Setting.address.delete')}}</div>
                </li>
            </ul>
        </div>
        <AddressPrompt v-show="addressPromptFlag" @closeAddressPrompt="closeAddressPrompt" @resetAddressList="resetAddressList" :currentAddressInfo="currentAddressInfo"></AddressPrompt>
    </div>
</template>

<script>
  import AddressPrompt from '../../../components/AddressPrompt'
  export default {
    name: 'Address',
    data() {
      return {
        addressList: [
          {
            id: '001',
            remark: 'BTC地址1',
            address: '152p1VPp2UYceP9BLtJRLoMgoErQRCryT'
          }
        ],
        addressPromptFlag: false,
        currentAddressInfo: null
      }
    },
    methods: {
      getAddressList() {
        // TODO: 接口获取地址列表
      },
      addAddress() {
        this.currentAddressInfo = null;
        this.showAddressPrompt();
      },
      updateAddress(item,index) {
        this.currentAddressInfo = {...item};
        this.currentAddressInfo.index = index;
        this.showAddressPrompt();
      },
      deleteAddress(item,index) {
        // 删除 本地操作 (非真实操作)
        this.addressList.splice(index,1);

        // TODO: delete 接口操作 (真实操作) 基于id
        // ...

        // TODO: delete 后重置 getAddressList
        // ...

      },
      resetAddressList(obj) {
        if (obj.index !== '') {
          // 修改 本地操作 (非真实操作)
          this.addressList[obj.index].address = obj.address;
          this.addressList[obj.index].remark = obj.remark;

          // TODO: update 接口操作 (真实操作) 基于id
          // ...

          // TODO: update 后重置 getAddressList
          // ...

        }else {
          // 添加 本地操作 (非真实操作)
          let {address,remark} = obj;
          this.addressList.unshift({address,remark});

          // TODO: add 接口操作  (真实操作)
          // ...

          // TODO: add 后重置 getAddressList
          // ...
        }
        // console.log('this.addressList',this.addressList);
        this.closeAddressPrompt();
      },
      showAddressPrompt() {
        this.addressPromptFlag = true;
      },
      closeAddressPrompt() {
        this.addressPromptFlag = false;
      },
    },
    mounted() {


    },
    components: {
      AddressPrompt
    }
  };
</script>

<style lang="less" scoped>
    .addressPage {
        margin: 0 auto 0.2rem;
        width: 7.3rem;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        .handleC {
            height: 1rem;
            background: rgba(255,255,255,1);
            box-shadow: 0 0.06rem 0.30rem 0 rgba(0,0,0,0.02);
            border-radius: 0.10rem;
            display: flex;
            align-items: center;
            .title {
                margin-left: 0.24rem;
                font-size: 0.28rem;
                color: rgba(151,155,162,1);
            }
            .addAddress {
                margin-left: auto;
                margin-right: 0.38rem;
                display: flex;
                align-items: center;
                & > span:first-child {
                    width: 0.44rem;
                    height: 0.44rem;
                    background: rgba(250,249,249,1);
                    border-radius: 0.04rem;
                    border: 1px solid rgba(235,235,235,1);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                /*& > span:first-child:before {*/
                /*content: '+';*/
                /*}*/
                & > span:last-child {
                    margin-left: 0.18rem;
                    font-size: 0.28rem;
                    color: rgba(0,0,0,1);
                }
            }
        }
        .addressListC {
            margin-top: 0.2rem;
            min-height: 6rem;
            background: rgba(255,255,255,1);
            border-radius: 0.10rem;
            padding: 0 0.1rem;
            .head {
                height: 1.12rem;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 0.30rem;
                color: rgba(0,0,0,1);
                border-bottom: 1px solid rgba(243, 243, 243, 1);
            }
            .addressList {
                & > li {
                    height: 1.4rem;
                    display: flex;
                    align-items: center;
                    border-bottom: 1px solid rgba(243, 243, 243, 1);
                    .addressMsg {
                        margin-left: 0.1rem;
                        width: 4rem;
                        .remark {
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            font-size: 0.28rem;
                            color: rgba(151,155,162,1);
                        }
                        .address {
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            font-size: 0.28rem;
                            color:rgba(0,0,0,1);
                        }
                    }
                    .update {
                        margin-left: auto;
                        margin-right: 0.2rem;
                        font-size: 0.28rem;
                        color: rgba(61, 121, 255, 1);
                        /*color: rgba(151,155,162,1);*/
                    }
                    .delete {
                        margin-right: 0.1rem;
                        font-size: 0.28rem;
                        color: rgba(61, 121, 255, 1);
                        /*color: rgba(151,155,162,1);*/
                    }
                }
            }

        }
    }
</style>


