<template>
    <div class="forgetPage" id="forgetPage">
        <div class="head">
            <div class="title">{{$t('ForgetPassword.title')}}</div>
        </div>
        <div class="resetPasswordC">
            <div class="hintText">{{$t('ForgetPassword.hintText')}}</div>
            <ul class="formList">
                <li class="selected formItem" @click="toggleCountryFind">
                    <div class="countryCode">{{currentCountry[currentNameType]}} +{{currentCountry.qh}}</div>
                    <span class="downIcon" v-show="!countryFindToggle"></span>
                    <span class="upIcon" v-show="countryFindToggle"></span>
                    <SearchCountry v-show="countryFindToggle"  @selecteCountry="selecteCountry"></SearchCountry>
                </li>
                <li class="phoneNumber formItem">
                    <input type="number" :placeholder="$t('ForgetPassword.phone_p')" v-model="phoneNum" oninput="if(value.length > 15)value = value.slice(0, 15)">
                </li>
                <li class="verifyCode formItem">
                    <input type="number" :placeholder="$t('ForgetPassword.verifyCode_p')" v-model="verifyCode" oninput="if(value.length > 6)value = value.slice(0, 6)">
                    <span class="getVCode" @click="getVerifyCode">{{$t('ForgetPassword.getVCode')}}</span>
                    <!--<span class="countDown">60s</span>-->
                </li>
                <li class="password formItem">
                    <input type="text" :placeholder="$t('ForgetPassword.password_p')" v-model="password">
                </li>
            </ul>
            <div class="btnContainer">
                <button @click="confirm" :class="{'act':confirmFlag}">{{$t('ForgetPassword.button')}}</button>
            </div>
            <div class="signUp" @click="toSignUpPage">{{$t('ForgetPassword.signUp')}}</div>
        </div>
    </div>
</template>

<script>
  import SearchCountry from '../../components/SearchCountry'
  export default {
    name: 'ForgetPassword',
    data() {
      return {
        countryFindToggle: false,
        currentNameType: 'name_zh',  // name_zh    name_en
        currentCountry: {
          name_en: 'China',
          name_zh: '中国',
          countrysmall: '', // 缩写
          qh: '86', // 手机码
          countryCode: '', // 国家码
          chou: '' // 洲
        },
        phoneMinLength: 3,
        phoneMaxLength: 15,
        phoneNum: '',
        verifyCode: '',
        password: '',  // 8-16数字和字母组成
        sendCodeStatus: false, // false 未发送  true 已发送
        sendCodeTimer: null,
        sendCodeCount: 0
      }
    },
    methods: {
      getCurrentNameType() {
        this.currentLanguageType = this.$store.state.currentLanguage.type;
        this.currentNameType = this.currentLanguageType === 'CN' ? 'name_zh' : 'name_en';
      },
      getCountryIP() {
        let url = '/community-user/ip';
        this.$ajax(url, {}, res => {
          if (res.data.success == true) {
            let _abbr;
            if (res.data.object) {
              _abbr = res.data.object.country_code;
            }else {
              _abbr = 'CN';
            }
            this.getCountryData(_abbr);
          }
        }, 'get');
      },
      getCountryData(abbr) {
        let url = '/community-user/findCountry';
        this.$ajax(url, {
          countrysmall: abbr,
        }, res => {
          console.log('res', res.data);
          if (res.data.success == true) {
            this.currentCountry = {...res.data.object[0]}
            // this.resetCountry(country);
          }
        }, 'get');
      },
      toSignUpPage() {
        this.$router.push({
          path: '/SignUp'
        })
      },
      getVerifyCode() {
        if (this.verifyCode) {
          this.verifyCode = '';
        }
        if (this.currentCountry.countrysmall === 'CN') {
          if ($Tool.checkMobile(this.phoneNum)) {
            // TODO： 测试
            this.sendCodeCount = 60;
            this.createSendCodeTimer();
            // this.sendCode();
          } else {
            $Tool.prompt(this.$t('Prompt.hint.phone'));
          }
        } else {
          if (this.phoneNum.length <= this.phoneMaxLength && this.phoneNum.length >= this.phoneMinLength) {
            // this.sendCode();
          } else {
            $Tool.prompt(this.$t('Prompt.hint.phone'));
          }
        }
      },
      sendCode() {
        if (this.sendCodeStatus) { return false; }
        this.sendCodeStatus = true;
        this.$ajax('/community-user/sendCode', {
          qh: this.currentCountry.qh,
          phone: this.phoneNum,
          type: 0, // 类型 0:用户注册、1:登入、2:忘记密码
        }, res => {
          if (res && res.data.success == true) {
            // code 1 已经注册   code 0 没有注册 发送成功
            if (res.data.code === 1) {
              this.sendCodeStatus = false;
              // TODO:  老用户 弹框提示
              console.log('老用户');
            } else if (res.data.code === 0) {
              // TODO:  新用户
              this.sendCodeStatus = true; // --> 倒计时为0  false
              this.sendCodeCount = 60;
              this.createSendCodeTimer();
              console.log('新用户');
            }
          }else {
            this.sendCodeStatus = false;
          }
        }, 'get');
      },
      createSendCodeTimer() {
        if (this.sendCodeTimer) {
          clearInterval(this.sendCodeTimer);
          this.sendCodeTimer = null;
        }
        this.sendCodeTimer = setInterval(() => {
          this.sendCodeCount--;
          if (this.sendCodeCount <= 0) {
            clearInterval(this.sendCodeTimer);
            this.sendCodeTimer = null;
            this.sendCodeCount = 0;
            this.sendCodeStatus = false;
          }
        }, 1000);
      },
      selecteCountry(item) {
        this.countryFindToggle = false;
        this.currentCountry = {...item};
      },
      toggleCountryFind() {
        this.countryFindToggle = !this.countryFindToggle;
      },
      confirm() {
        if(!this.confirmFlag) {return false}
        this.$ajax('/windmine-user/forgetpwd', {
          phone: this.phoneNum,
          qh: this.currentCountry.qh,
          sms: this.verifyCode,
          pwd: this.password
        }, res => {
          if (res.data.success == true) {
            // TODO: 返回登录页 待
          }
        }, 'post');
      }
    },
    mounted() {
      // TODO: 获取语言类型
      this.getCurrentNameType();
      // TODO: 获取所在地IP
      this.getCountryIP();
    },
    computed: {
      confirmFlag: function () {
        let _flag;
        if (this.currentCountry.countrysmall === 'CN') {
          _flag = $Tool.checkMobile(this.phoneNum) && $Tool.checkCode(this.verifyCode) && $Tool.checkPassword(this.password)
        }else {
          _flag = (this.phoneNum.length >= this.phoneMinLength && this.phoneNum.length <= this.phoneMaxLength) && $Tool.checkCode(this.verifyCode) && $Tool.checkPassword(this.password)
        }
        return _flag
      }
    },
    components: {
      SearchCountry
    }
  };
</script>

<style lang="less" scoped>
    #forgetPage {
        position: absolute;
        width: 100%;
        height: 100%;
        .head {
            height: 3.4rem;
            background-color: rgba(22,22,22,1);
            overflow: hidden;
            .title {
                margin-top: 1.1rem;
                text-align: center;
                font-size: 0.50rem;
                font-family: Helvetica-Bold;
                font-weight: bold;
                color: rgba(255,255,255,1);
            }
        }
        .resetPasswordC {
            /*position: absolute;*/
            /*left: 0;*/
            /*bottom: 0;*/
            margin-top: -0.7rem;
            width: 100%;
            height: 10.90rem;
            background: rgba(255,255,255,1);
            box-shadow: 0 -0.04rem 0.04rem 0 rgba(0,0,0,0.5);
            border-radius: 0.70rem 0.70rem 0 0;
            overflow: hidden;
            .hintText {
                margin: 0.44rem auto 0;
                width: 6.3rem;
                font-size: 0.50rem;
                font-family: PingFangSC-Semibold;
                font-weight: 600;
                color: rgba(0,0,0,1);
            }
            .formList {
                margin: 0.66rem auto 0;
                width: 6.3rem;
                & > li {
                    height: 0.90rem;
                    margin-bottom: 0.2rem;
                    /*padding-left: 0.5rem;*/
                    background-color: rgba(242,243,247,1);
                    border-radius: 0.45rem;
                    display: flex;
                    align-items: center;
                    & > input {
                        width: 3.80rem;
                        height: 0.42rem;
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        background-color: inherit;
                        margin-left: 0.5rem;
                        /*background-color: cyan;*/
                    }
                }
                .selected {
                    position: relative;
                    .countryCode {
                        width: 4.80rem;
                        height: 0.42rem;
                        /*background-color: white;*/
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(103,103,103,1);
                        margin-left: 0.5rem;
                    }
                    .downIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.11rem solid transparent;
                        border-right: 0.11rem solid transparent;
                        border-top: 0.18rem solid rgba(188,188,188,1);
                        /*margin: 0 0.4rem 0 auto;*/
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .upIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.18rem solid rgba(188,188,188,1);
                        border-top: 0.11rem solid transparent;
                        border-bottom: 0.11rem solid transparent;
                        margin: 0 0.4rem 0 auto;
                    }
                }
                .verifyCode {
                    .getVCode {
                        width: 1.40rem;
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .countDown {
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                }
            }
            .btnContainer {
                margin: 0.62rem auto 0;
                width: 6.3rem;
                padding-bottom: 0.62rem;
                text-align: center;
                border-bottom: 1px solid rgba(242,243,247,1);
                button {
                    /*display: flex;*/
                    /*align-items: center;*/
                    /*justify-content: center;*/
                    /*margin: 0 auto;*/
                    width: 6.30rem;
                    height: 0.96rem;
                    background-color: rgba(61,108,255,1);
                    border: 0;
                    box-shadow: 0 0.08rem 0.2rem -0.09rem rgba(61,121,255,0.5);
                    border-radius: 0.48rem;
                    font-size: 0.32rem;
                    font-family: HelveticaNeue;
                    color: rgba(255,255,255,1);
                    opacity: 0.5;
                }
                button.act {
                    opacity: 1;
                }
            }
            .signUp {
                margin-top: 0.52rem;
                text-align: center;
                font-size: 0.28rem;
                font-family: PingFangSC-Medium;
                font-weight: 500;
                color: rgba(61,121,255,1);
            }
        }
        input {
            border: 0;
        }
    }
</style>


