<template>
    <div class="countryFindC" @click.stop>
        <div class="head">
            <div class="searchC">
                <span class="searchIcon"></span>
                <input type="text" v-model="searchContent" placeholder="国家/地区名">
            </div>
        </div>
        <ul class="countryList">
            <li class="countryItem" v-for="(item,index) in showCountryList" :key="index" @click="selecteCountry(item)">
                <span class="phoneCode">+&nbsp;{{item.qh}}</span>
                <span class="countryName">{{item[currentNameType]}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
  export default {
    name: 'SearchCountry',
    data() {
     return {
       showCountryList: [],
       totalCountryList: [],
       searchContent: '',
       currentNameType: 'name_zh',  // name_zh    name_en
     }
    },
    methods: {
      getCountryList() {
        let url = '/community-user/findCountry';
        this.$ajax(url, {}, res => {
          console.log('res',res.data);
          if (res.data.success == true) {
            this.totalCountryList = [...res.data.object];
            this.showCountryList = [...res.data.object];
            // console.log('this.showCountryList',this.showCountryList);
          }
        });
      },
      getSearchList(val) {
        let url = '/community-user/findCountry';
        this.$ajax(url, {
          country: val
        }, res => {
          console.log('res',res.data);
          if (res.data.success == true) {
            this.showCountryList = [...res.data.object];
            // console.log('this.showCountryList',this.showCountryList);
          }
        },'get');
      },
      selecteCountry(item) {
        this.$emit('selecteCountry',item);
        // this.countryFindToggle = false;
        // this.currentCountry = {...item};
      },
      getCurrentNameType() {
        this.currentNameType = this.$store.state.currentLanguage.type === 'CN' ? 'name_zh' : 'name_en';
      }
    },
    mounted() {
      this.getCurrentNameType();
      this.getCountryList();
    },
    watch: {
      searchContent: function(val){
        console.log('searchContent',val);
        if(!val) {
          this.showCountryList = [...this.totalCountryList];
        }else {
          this.getSearchList(val);
        }
      }
    }

  };
</script>

<style lang="less" scoped>
    .countryFindC {
        top: 0.8rem;
        left: 0;
        position: absolute;
        width: 6.30rem;
        background-color: rgba(255,255,255,1);
        box-shadow: 0 0.04rem 0.40rem 0 rgba(224,225,230,1);
        border-radius: 0.20rem;
        border:1px solid rgba(225,225,225,1);
        z-index: 99;
        .head {
            height: 0.90rem;
            background-color: rgba(244,245,246,1);
            border-radius: 0.20rem 0.20rem 0 0;
            display: flex;
            align-items: center;
            justify-content: center;
            .searchC {
                width: 5.80rem;
                height: 0.70rem;
                background-color: rgba(255,255,255,1);
                border-radius: 0.35rem;
                border:1px solid rgba(237,237,237,1);
                display: flex;
                align-items: center;
                /*justify-content: center;*/
                .searchIcon {
                    margin: 0 0.2rem 0 0.2rem;
                    width: 0.38rem;
                    height: 0.38rem;
                    background: url("../assets/img/searchIcon.png") no-repeat center center;
                    background-size: cover;
                }
                input {
                    /*background-color: cyan;*/
                    font-size: 0.30rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color: rgba(51,51,51,1);
                }
            }
        }
        .countryList {
            height: 5.7rem;
            overflow: auto;
            -webkit-overflow-scrolling: touch;// iOS 兼容惯性滚动
            & > li {
                height: 0.8rem;
                border-bottom: 1px solid rgba(237,238,243,1);
                display: flex;
                align-items: center;
                /*justify-content: center;*/
                & > span {
                    font-size: 0.30rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color: rgba(51,51,51,1);
                }
                & > span.phoneCode {
                    margin-left: 0.4rem;
                    width: 1.5rem;
                }
            }
        }
        input {
            border: 0;
        }
    }
</style>


