import Vue from 'vue'
import Vuex from 'vuex'
import Config from '../config'
import $Tool from '../api/util';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    // 代替data
    currentLanguage: {type:'CN',name:'中文'}
  },
  getters: {
    // 代替计算属性
  },
  mutations: {
    setCurrentLanguage(state,language) {
      Object.assign(state.currentLanguage,language);
      // state.currentLanguage = language;
    },
  },
  actions: {
    // 异步修改状态 -- 通过接口来维护状态
  }

});

if (Config.terminalMode !== 'Android') {
  getBowerLanguage();
}else {
  getURLLanguage();
}

//TODO: 获取参数，根据参数判断语言类型
function getURLLanguage() {
  let lv = $Tool.getQueryString('lv');
  console.log('lv',lv);
  if (lv === '0') {
    // 英文
    store.state.currentLanguage.type = 'EN';
    store.state.currentLanguage.name = 'English';
  }else {
    store.state.currentLanguage.type = 'CN';
    store.state.currentLanguage.name = '中文';
  }
}

// TODO: 根据终端判断语言类型
function getBowerLanguage() {
  let lang = window.navigator.language || window.navigator.userLanguage; //常规浏览器语言和IE浏览器
  lang = lang.substr(0, 2); //截取lang前2位字符
  console.log('lang',lang);
  if (lang === 'zh') {
    store.state.currentLanguage.type = 'CN';
    store.state.currentLanguage.name = '中文';
  } else {
    store.state.currentLanguage.type = 'EN';
    store.state.currentLanguage.name = 'English';
  }
}


export default store;


