                        <div class="title">{{$t('Payment.orderDetail.orderId.title')}}</div>
                        <div class="value">{{orderInfo.orderId}}</div>
                    </div>
                    <div class="productPrice">
                        <div class="title">{{$t('Payment.orderDetail.productPrice.title')}}</div>
                        <div class="value">${{orderInfo.productPrice}}</div>
                    </div>
                    <div class="commissionDeduction">
                        <div class="title">{{$t('Payment.orderDetail.commission.title')}}</div>
                        <div class="value">${{orderInfo.commissionDeduction}}</div>
                    </div>
                    <!--TODO: 金币抵扣 暂无-->
                    <div class="deductionPrice" v-if="false">
                        <div class="title">{{$t('Payment.orderDetail.deductionPrice.title')}}</div>
                        <div class="value">-${{orderInfo.deductionPrice}}</div>
                    </div>
                    <!--TODO: 优惠券抵扣 暂无-->
                    <div class="couponPrice" v-if="false">
                        <div class="title">{{$t('Payment.orderDetail.couponPrice.title')}}</div>
                        <div class="value">-${{orderInfo.couponPrice}}</div>
                    </div>
                    <div class="payPrice">
                        <div class="title">{{$t('Payment.orderDetail.payPrice.title')}}</div>
                        <div class="value">${{orderInfo.payPrice}}</div>
                    </div>
                </div>
            </div>
            <div class="payMethodC">
                <div class="head">
                    <div class="title">{{$t('Payment.payMethod.title')}}</div>
                </div>
                <div class="content">
                    <div class="alipay"><img src="../../assets/img/alipayIcon.png" alt=""></div>
                    <div class="paypal"><img src="../../assets/img/paypalIcon.png" alt=""></div>
                    <div class="usdt"><img src="../../assets/img/usdtIcon.png" alt=""></div>
                </div>
            </div>
            <div class="agreementC">
                <input type="checkbox" v-model="agreementFlag">
                <span>{{$t('Payment.agreement.one')}}</span>
                <span class="agreementLink" @click="toAgreementPage">{{$t('Payment.agreement.two')}}</span>
            </div>
            <div class="confirmC">
