                    <div class="title">{{$t('Payment.confirm.pay')}}</div>
                    <div class="payValue">$ {{orderInfo.payPrice}}</div>
                </div>
                <button class="confirmBtn" :class="{'act': agreementFlag }">{{$t('Payment.confirm.sure')}}</button>
            </div>
            <div class="digStartTimes">{{$t('Payment.digStartTime.title')}} {{orderInfo.digStartTimes}}</div>
        </div>
        <Attention></Attention>
        <Footer></Footer>
    </div>
