<script>
  export default {
    name: 'Payment',
    data() {
      return {
        orderInfo: {
          orderId: '00001', // 订单id
          productPrice: '24', // 商品费用
          remainTime: '11 : 50 : 20', // 剩余时间
          payPrice: '19', // 支付金额
          commissionDeduction: '5',
          digStartTimes: '19.8.30 08:00', // 预计挖矿开始时
          // 不确定项，可能无
          deductionPrice: '5', // 金币抵扣
          couponPrice: '1', // 优惠券抵扣
