<template>
    <div class="footer">
        <div class="title">WINDMINE</div>
        <div class="shareC">
            <img class="Instagram" src="../assets/img/InstagramIcon.png" alt="" @click="toShareURL('Instagram')">
            <img class="Telegram" src="../assets/img/TelegramIcon.png" alt=""  @click="toShareURL('Telegram')">
            <img class="Twitter" src="../assets/img/TwitterIcon.png" alt="" @click="toShareURL('Twitter')">
            <img class="Facebook" src="../assets/img/FacebookIcon.png" alt="" @click="toShareURL('Facebook')">
            <img class="Skype" src="../assets/img/SkypeIcon.png" alt="" @click="toShareURL('Skype')">
            <img class="Linked" src="../assets/img/LinkedIcon.png" alt="" @click="toShareURL('Linked')">
        </div>
        <div class="copyright">COPYRIGHT @2019  WINDMIND ALL RIGHTS RESERVED</div>
    </div>
</template>

<script>
  export default {
    name: 'Footer',
    data() {
      return {
        shareList: {
          Instagram: 'http://instagram.com/windtalktoken',
          Telegram: 'https://t.me/windtalktoken',
          Twitter: 'https://twitter.com/windtalktoken',
          Facebook: 'https://www.facebook.com/profile.php?id=100026944451707',
          Skype: 'https://join.skype.com/invite/QNelRagcIvPW',
          Linked: 'https://www.linkedin.com/company/wind-communication-group/'
        }
      }
    },
    methods: {
      toShareURL(type) {
        window.open(this.shareList[type],"_blank");
      }
    }
  };
</script>

<style lang="less" scoped>
    .footer {
        height: 3.30rem;
        background-color: rgba(35,35,35,1);
        overflow: hidden;
        .title {
            margin-top: 0.5rem;
            text-align: center;
            font-size: 0.40rem;
            font-family: Helvetica-Bold;
            font-weight: bold;
            color: rgba(66,66,66,1);
        }
        .shareC {
            margin-top: 0.9rem;
            display: flex;
            align-items: center;
            justify-content: center;
            padding-bottom: 0.24rem;
            border-bottom: 1px solid rgba(50,50,50,1);
            img.Instagram { width: 0.4rem; margin-right: 0.37rem}
            img.Telegram { width: 0.4rem; margin-right: 0.47rem}
            img.Twitter { width: 0.33rem; margin-right: 0.57rem}
            img.Facebook { width: 0.18rem; margin-right: 0.54rem}
            img.Skype { width: 0.33rem; margin-right: 0.47rem}
            img.Linked { width: 0.37rem}
        }
        .copyright {
            margin-top: 0.26rem;
            text-align: center;
            font-size: 0.20rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(93,93,93,1);
        }
    }
</style>


