<template>
    <div class="safePage">
        <div class="passwordC">
            <div class="title">{{$t('Setting.safe.password.title')}}</div>
            <div class="update" @click="updatePassword">{{$t('Setting.safe.password.update')}}</div>
        </div>
        <PasswordPrompt v-show="passwordPromptFlag" @closePasswordPrompt="closePasswordPrompt"></PasswordPrompt>
    </div>
</template>

<script>
  import PasswordPrompt from '../../../components/PasswordPrompt'
  export default {
    name: 'Safe',
    data() {
      return {
        passwordPromptFlag: false
      }
    },
    methods: {
      updatePassword() {
        this.showPasswordPrompt();
      },
      showPasswordPrompt() {
        this.passwordPromptFlag = true;
      },
      closePasswordPrompt() {
        this.passwordPromptFlag = false;
      }

    },
    mounted() {

    },
    components: {
      PasswordPrompt
    }
  };
</script>

<style lang="less" scoped>
    .safePage {
        margin: 0.2rem auto 0.2rem;
        width: 7.30rem;
        min-height: 7rem;
        background: rgba(255,255,255,1);
        box-shadow: 0 0.06rem 0.30rem 0 rgba(0,0,0,0.02);
        border-radius: 0.10rem;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        .passwordC {
            width: 7.10rem;
            margin: 0 auto;
            height: 1rem;
            display: flex;
            align-items: center;
            border-bottom: 1px solid rgba(243, 243, 243, 1);
            .title {
                margin-left: 0.14rem;
                font-size: 0.28rem;
                color: rgba(151,155,162,1);
            }
            .update {
                margin-left: auto;
                margin-right: 0.28rem;
                font-size: 0.28rem;
                color: rgba(0,0,0,1);
            }
        }
    }
</style>


