<template>
    <div class="layer" @touchmove.prevent>
        <div class="promptC">
            <div class="head">{{$t('Prompt.address.head["'+currentType+'"]')}}</div>
            <div class="content">
                <div class="addressC">
                    <input type="text" class="address" v-model="addressInfo.address"
                           :placeholder="$t('Prompt.address.address_p')">
                </div>
                <div class="remarkC">
                    <input type="text" class="remark" v-model="addressInfo.remark"
                           :placeholder="$t('Prompt.address.remark_p')">
                </div>
                <!--<div class="verifyCodeC">-->
                    <!--<input type="number" class="verifyCode" v-model="verifyCode"-->
                           <!--:placeholder="$t('Prompt.address.verifyCode_p')">-->
                    <!--<span class="handle">获取验证码</span>-->
                <!--</div>-->
            </div>
            <div class="footer">
                <button class="left" @click="handleLeftEvent">{{$t('Prompt.address.btn.left')}}</button>
                <button class="right act" @click="handleRightEvent">{{$t('Prompt.address.btn.right')}}</button>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'AddressPrompt',
    data() {
      return {
        currentType: 'add',
        addressInfo: {
          id: '',
          type: 'BTC',
          address: '',
          remark: '',
          index: '' // 为空则表示 新增
        },
        verifyCode: ''
      };
    },
    methods: {
      handleLeftEvent() {
        this.$emit('closeAddressPrompt')
      },
      handleRightEvent() {
        if (!this.addressInfo.address) { $Tool.prompt(this.$t('Prompt.hint.address')); return false}
        this.$emit('resetAddressList',this.addressInfo);
        // 添加完清除
        this.clearAddressInfo();
      },
      getCurrentAddressInfo() {
        if (this.currentAddressInfo) {
          this.addressInfo = Object.assign(this.addressInfo,this.currentAddressInfo);
          this.currentType = 'update'
        }else {
          this.addressInfo.address = '';
          this.addressInfo.remark = '';
          this.currentType = 'add'
        }
      },
      clearAddressInfo() {
        let {keys, values, entries} = Object;
        for (let key of keys(this.addressInfo)) {
          if (key !== 'type') {
            this.addressInfo[key] = '';
          }
        }
      }
    },
    mounted() {
      console.log('currentAddressInfo',this.currentAddressInfo);
      this.getCurrentAddressInfo();
    },
    props: {
      currentAddressInfo: {
        type: Object
      }
    },
    watch: {
      'currentAddressInfo': function (val,oldValue) {
        this.getCurrentAddressInfo();
      }

    }
  };
</script>

<style lang="less" scoped>
    .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 99;
        .promptC {
            margin: 0 auto;
            width: 6.90rem;
            height: 4.70rem;
            background: rgba(255, 255, 255, 1);
            border-radius: 0.20rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .head {
                height: 1.46rem;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 0.40rem;
                font-family: PingFangSC-Semibold;
                font-weight: 600;
                color: rgba(32, 47, 66, 1);

            }
            .content {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-bottom: 0.3rem;
                & > div {
                    border: 1px solid rgba(235, 235, 235, 1);
                    width: 6.20rem;
                    height: 0.80rem;
                    margin-bottom: 0.2rem;
                    padding-left: 0.2rem;
                    background: rgba(250, 249, 249, 1);
                    & > input {
                        width: 100%;
                        height: 100%;
                        background: inherit;
                        border-radius: 0.04rem;
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: #000000;
                    }
                }
            }
            .footer {
                margin-top: auto;
                width: 100%;
                height: 1.0rem;
                border-top: 1px solid rgba(216, 216, 216, 1);
                display: flex;
                align-items: center;
                justify-content: center;
                & > button {
                    width: 50%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 0.34rem;
                    color: rgba(0, 0, 0, 1);
                    background-color: inherit;
                }
                & > button:first-child {
                    border-right: 1px solid rgba(216, 216, 216, 1)
                }
                & > button.act {
                    color: rgba(61, 121, 255, 1);
                }

            }
        }

    }
</style>


