<template>
    <div class="signUpPage" id="signUpPage">
        <div class="head">
            <div class="title">{{$t('SignUp.title')}}</div>
        </div>
        <div class="signUpContainer">
            <div class="hintText">{{$t('SignUp.hintText')}}</div>
            <ul class="formList">
                <li class="selected formItem" @click="toggleCountryFind">
                    <div class="countryCode">{{currentCountry[currentNameType]}} +{{currentCountry.qh}}</div>
                    <span class="downIcon" v-show="!countryFindToggle"></span>
                    <span class="upIcon" v-show="countryFindToggle"></span>
                    <SearchCountry v-show="countryFindToggle"  @selecteCountry="selecteCountry"></SearchCountry>
                </li>
                <li class="phoneNumber formItem">
                    <input type="number" :placeholder="$t('SignUp.phone_p')" v-model="phoneNum" oninput="if(value.length > 15)value = value.slice(0, 15)">
                </li>
                <li class="verifyCode formItem">
                    <input type="number" :placeholder="$t('SignUp.verifyCode_p')" v-model="verifyCode" oninput="if(value.length > 6)value = value.slice(0, 6)">
                    <span class="getVCode" @click="getVerifyCode" v-if="sendCodeCount === 0">{{$t('SignUp.getVCode')}}</span>
                    <span class="countDown" v-else>{{sendCodeCount}}s</span>
                </li>
                <li class="password formItem">
                    <input type="text" :placeholder="$t('SignUp.password_p')" v-model="password">
                </li>
            </ul>
            <div class="inviteCode">
                <input type="text" maxlength="6" :placeholder="$t('SignUp.inviteCode_p')">
            </div>
            <div class="btnContainer">
                <button @click="signUp" :class="{'act':signUpFlag}">{{$t('SignUp.button')}}</button>
            </div>
            <div class="protocol">
                {{$t('SignUp.protocol')}}
            </div>
            <div class="signIn" @click="toSignInPage">{{$t('SignUp.signIn')}}</div>
        </div>
        <CommonPrompt v-show="commonPromptFlag" @closeCommonPrompt="closeCommonPrompt"></CommonPrompt>
    </div>
</template>

<script>
  import SearchCountry from '../../components/SearchCountry'
  import CommonPrompt from '../../components/CommonPrompt'
  import $Tool from '../../api/util';
  export default {
    name: 'SignUp', // 注册
    data() {
      return {
        countryFindToggle: false,
        currentLanguageType: 'CN',
        currentNameType: 'name_zh',  // name_zh    name_en
        currentCountry: {
          name_en: 'China',
          name_zh: '中国',
          countrysmall: '', // 缩写
          qh: '86', // 手机码
          countryCode: '', // 国家码
          chou: '' // 洲
        },
        phoneMinLength: 3,
        phoneMaxLength: 15,
        sendCodeCount: 0,
        phoneNum: '',
        verifyCode: '',
        password: '',  // 8-16数字和字母组成
        sendCodeStatus: false, // false 未发送  true 已发送
        sendCodeTimer: null,
        promptText: {
          phone: {
            CN: '手机号错误',
            EN: 'Phone number error',
          },
          codeTimeout: {
            CN: '验证码超时',
            EN: 'SMS code timeout',
          },
          codeError: {
            CN: '验证码错误',
            EN: 'SMS code error',
          },
          hadRegister: {
            CN: '用户已注册',
            EN: '',
          },
          countryCodeError: {
            CN: '国家码错误',
            EN: '',
          },
          unusual: {
            CN: '异常',
            EN: '',
          },
        },
        commonPromptFlag: false,
        // signUpFlag: false
      }
    },
    methods: {
      toSignInPage() {
        this.$router.push({
          path: '/SignIn'
        })
      },
      getCurrentNameType() {
        this.currentLanguageType = this.$store.state.currentLanguage.type;
        this.currentNameType = this.currentLanguageType === 'CN' ? 'name_zh' : 'name_en';
      },
      getCountryIP() {
        let url = '/community-user/ip';
        this.$ajax(url, {}, res => {
          if (res.data.success == true) {
            let _abbr;
            if (res.data.object) {
              _abbr = res.data.object.country_code;
            }else {
              _abbr = 'CN';
            }
            this.getCountryData(_abbr);
          }
        }, 'get');
      },
      getCountryData(abbr) {
        let url = '/community-user/findCountry';
        this.$ajax(url, {
          countrysmall: abbr,
        }, res => {
          console.log('res', res.data);
          if (res.data.success == true) {
            this.currentCountry = {...res.data.object[0]}
            // this.resetCountry(country);
          }
        }, 'get');
      },
      getVerifyCode() {
        if (this.verifyCode) {
          this.verifyCode = '';
        }
        if (this.currentCountry.countrysmall === 'CN') {
          if ($Tool.checkMobile(this.phoneNum)) {
            // TODO： 测试
            this.sendCodeCount = 60;
            this.createSendCodeTimer();
            // this.sendCode();
          } else {
            $Tool.prompt(this.$t('Prompt.hint.phone'));
          }
        } else {
          if (this.phoneNum.length <= this.phoneMaxLength && this.phoneNum.length >= this.phoneMinLength) {
            // this.sendCode();
          } else {
            $Tool.prompt(this.$t('Prompt.hint.phone'));
          }
        }
      },
      sendCode() {
        if (this.sendCodeStatus) { return false; }
        this.sendCodeStatus = true;
        this.$ajax('/community-user/sendCode', {
          qh: this.currentCountry.qh,
          phone: this.phoneNum,
          type: 0 // 类型 0:用户注册、1:登录、2:忘记密码
        }, res => {
          if (res && res.data.success == true) {
            // code 1 已经注册   code 0 没有注册 发送成功
            if (res.data.code === 1) {
              this.sendCodeStatus = false;
              // TODO:  老用户 弹框提示
              // this.commonPromptFlag = true;
              this.showCommonPrompt();
              console.log('老用户');
            } else if (res.data.code === 0) {
              // TODO:  新用户
              this.sendCodeStatus = true; // --> 倒计时为0  false
              this.sendCodeCount = 60;
              this.createSendCodeTimer();
              console.log('新用户');
            }
          }else {
            this.sendCodeStatus = false;
          }
        }, 'get');
      },
      createSendCodeTimer() {
        if (this.sendCodeTimer) {
          clearInterval(this.sendCodeTimer);
          this.sendCodeTimer = null;
        }
        this.sendCodeTimer = setInterval(() => {
          this.sendCodeCount--;
          if (this.sendCodeCount <= 0) {
            clearInterval(this.sendCodeTimer);
            this.sendCodeTimer = null;
            this.sendCodeCount = 0;
            this.sendCodeStatus = false;
          }
        }, 1000);
      },
      toggleCountryFind() {
        this.countryFindToggle = !this.countryFindToggle;
      },
      selecteCountry(item) {
        this.countryFindToggle = false;
        this.currentCountry = {...item};
      },
      showCommonPrompt() {
        this.commonPromptFlag = true;
      },
      closeCommonPrompt() {
        this.commonPromptFlag = false;
      },
      signUp() {
        /*
        * 注册情况分析:
        * 1.获取验证码，是新用户，输入密码后，完成注册
        * 2.获取验证码，是老用户，弹框提示
        * */
        if (!this.signUpFlag) {return false}
        this.$ajax('/windmine-user/regist', {
          phone: this.phoneNum,
          qh: this.currentCountry.qh,
          code: this.verifyCode,
          pwd: this.password,
          inviteCode: this.inviteCode
        }, res => {
          if (res.data.success == true) {
            // TODO: 获取token
            // TODO: 跳转首页
          }
        }, 'post');
      }

    },
    mounted() {
      // TODO: 获取语言类型
      this.getCurrentNameType();
      // TODO: 获取所在地IP
      this.getCountryIP();
    },
    computed: {
      signUpFlag: function () {
        let _flag = false;
        if (this.currentCountry.countrysmall === 'CN') {
          _flag = $Tool.checkMobile(this.phoneNum) && $Tool.checkCode(this.verifyCode) && $Tool.checkPassword(this.password)
        }else {
          _flag = (this.phoneNum.length >= this.phoneMinLength && this.phoneNum.length <= this.phoneMaxLength) && $Tool.checkCode(this.verifyCode) && $Tool.checkPassword(this.password)
        }
        // console.log('flag',_flag);
        return _flag;
      }
    },
    components: {
      SearchCountry,
      CommonPrompt
    }
  };
</script>

<style lang="less" scoped>
    #signUpPage {
        position: absolute;
        width: 100%;
        height: 100%;
        .head {
            height: 3.4rem;
            background-color: rgba(22,22,22,1);
            overflow: hidden;
            .title {
                margin-top: 1.1rem;
                text-align: center;
                font-size: 0.50rem;
                font-family: Helvetica-Bold;
                font-weight: bold;
                color: rgba(255,255,255,1);
            }
        }
        .signUpContainer {
            /*position: absolute;*/
            /*left: 0;*/
            /*bottom: 0;*/
            margin-top: -0.7rem;
            width: 100%;
            height: 10.90rem;
            background: rgba(255,255,255,1);
            box-shadow: 0 -0.04rem 0.04rem 0 rgba(0,0,0,0.5);
            border-radius: 0.70rem 0.70rem 0 0;
            overflow: hidden;
            .hintText {
                margin: 0.44rem auto 0;
                width: 6.3rem;
                font-size: 0.50rem;
                font-family: PingFangSC-Semibold;
                font-weight: 600;
                color: rgba(0,0,0,1);
            }
            .formList {
                margin: 0.66rem auto 0;
                width: 6.3rem;
                & > li {
                    height: 0.90rem;
                    margin-bottom: 0.2rem;
                    /*padding-left: 0.5rem;*/
                    background-color: rgba(242,243,247,1);
                    border-radius: 0.45rem;
                    display: flex;
                    align-items: center;
                    & > input {
                        width: 3.80rem;
                        height: 0.42rem;
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        background-color: inherit;
                        margin-left: 0.5rem;
                        /*background-color: cyan;*/
                    }
                }
                .selected {
                    position: relative;
                    .countryCode {
                        width: 4.80rem;
                        height: 0.42rem;
                        /*background-color: white;*/
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(103,103,103,1);
                        margin-left: 0.5rem;
                    }
                    .downIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.11rem solid transparent;
                        border-right: 0.11rem solid transparent;
                        border-top: 0.18rem solid rgba(188,188,188,1);
                        /*margin: 0 0.4rem 0 auto;*/
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .upIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.18rem solid rgba(188,188,188,1);
                        border-top: 0.11rem solid transparent;
                        border-bottom: 0.11rem solid transparent;
                        margin: 0 0.4rem 0 auto;
                    }
                }
                .verifyCode {
                    .getVCode {
                        width: 1.40rem;
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .countDown {
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                        width: 0.5rem;
                        text-align: right;
                    }
                }

            }
            .inviteCode {
                margin: 0 auto;
                width: 5.6rem;
                height: 0.8rem;
                /*background-color: cyan;*/
                display: flex;
                align-items: center;
                justify-content: center;
                border-bottom: 1px solid #F0F0F0;
                input {
                    width: 3.8rem;
                    height: 0.42rem;
                    text-align: center;
                    font-size: 0.26rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    /*color: rgba(181,183,191,1);*/

                }
            }
            .btnContainer {
                margin: 0.62rem auto 0;
                width: 6.3rem;
                padding-bottom: 0.26rem;
                text-align: center;
                border-bottom: 1px solid #F0F0F0;
                font-family: HelveticaNeue;
                button {
                    /*display: flex;*/
                    /*align-items: center;*/
                    /*justify-content: center;*/
                    /*margin: 0 auto;*/
                    width: 6.30rem;
                    height: 0.96rem;
                    background-color: rgba(61,108,255,1);
                    border: 0;
                    box-shadow: 0 0.08rem 0.2rem -0.09rem rgba(61,121,255,0.5);
                    border-radius: 0.48rem;
                    font-size: 0.32rem;
                    color: rgba(255,255,255,1);
                    opacity: 0.5;
                }
                button.act {
                    opacity: 1;
                }

            }
            .protocol {
                margin: 0.22rem auto 0.26rem;
                /*width: 5.75rem;*/
                width: 7rem;
                text-align: center;
                font-size: 0.28rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color: rgba(51,51,51,1);
            }
            .signIn {
                margin-bottom: 0.5rem;
                text-align: center;
                font-size: 0.28rem;
                font-family: PingFangSC-Medium;
                font-weight: 500;
                color: rgba(61,121,255,1);
            }
        }
        input {
            border: 0;
        }
    }

</style>


