<template>
    <div class="inviteRewardPage">
        <div class="inviteRewardC">
            <div class="inviteHead">
                <div class="hadInvite">
                    <div class="title">{{$t('Personal.inviteReward.hadInvite.title')}}</div>
                    <div class="value">{{inviteInfo.inviteNum}}人</div>
                </div>
                <div class="hadOrder">
                    <div class="title">{{$t('Personal.inviteReward.hadOrder.title')}}</div>
                    <div class="value">{{inviteInfo.orderNum}}人</div>
                </div>
            </div>
            <div class="commissionC">
                <div class="title">{{$t('Personal.inviteReward.commission.title')}}</div>
                <div class="valueC">
                    <div class="cash">{{$t('Personal.inviteReward.commission.cash')}}：${{commission.dollar}}</div>
                    <div class="coin">{{$t('Personal.inviteReward.commission.coin')}}：{{commission.coin}}</div>
                </div>
            </div>
            <div class="inviteCodeC">
                <div class="title">{{$t('Personal.inviteReward.inviteCode.title')}}</div>
                <div class="content">
                    <input type="text" :value="userInfo.inviteCode" readonly>
                    <span class="copy"  v-clipboard:copy="userInfo.inviteCode" v-clipboard:success="onCopySuccess" v-clipboard:error="onCopyError">{{$t('Personal.inviteReward.copy')}}</span>
                </div>
            </div>
            <div class="linkC">
                <div class="title">{{$t('Personal.inviteReward.link.title')}}</div>
                <div class="content">
                    <input type="text" :value="userInfo.inviteLink" readonly>
                    <span class="copy" v-clipboard:copy="userInfo.inviteLink" v-clipboard:success="onCopySuccess" v-clipboard:error="onCopyError">{{$t('Personal.inviteReward.copy')}}</span>
            </div>
            </div>
            <div class="rewardC">
                <div class="title">{{$t('Personal.inviteReward.reward.title')}}</div>
                <div class="content">{{$t('Personal.inviteReward.reward.content')}}</div>
            </div>
        </div>
        <div class="inviteListC">
            <ul class="inviteHeadList">
                <li v-for="(item,index) in inviteHeadList" :key="index" :class="{'act': currentListType === item.type}" @click="selectListType(item.type)">{{$t('Personal.inviteReward.headList["'+item.type+'"]')}}</li>
            </ul>
            <div class="invitePersonListC inviteContentC" v-show="currentListType === 'invite'">
                <ul class="invitePersonList inviteContentList" v-if="invitePersonList.length">
                    <li v-for="(item,index) in invitePersonList" :key="index">
                        <div class="left">
                            <div class="title">{{$t('Personal.inviteReward.invitePersonList.title')}}</div>
                            <div class="value">{{item.nickname}}</div>
                        </div>
                        <div class="right">
                            <div class="time">{{item.time}}</div>
                            <div class="phoneC">
                                <span class="qh">+{{item.qh}}</span>
                                <span class="value"> {{item.phone}}</span>
                            </div>
                        </div>
                    </li>
                </ul>
                <div class="noRecord" v-else>
                    <img src="../../../assets/img/noRecordIcon.png" alt="">
                    <span>{{$t('noRecord')}}</span>
                </div>
            </div>
            <div class="orderRewardListC inviteContentC" v-show="currentListType === 'order'">
                <ul class="orderRewardList inviteContentList" v-if="orderRewardList.length">
                    <li v-for="(item,index) in orderRewardList" :key="index">
                        <div class="left">
                            <div class="title">{{$t('Personal.inviteReward.orderRewardList.title')}}</div>
                            <div class="value">${{item.commission}}</div>
                        </div>
                        <div class="right">
                            <div class="time">{{item.time}}</div>
                            <div class="phoneC">
                                <span class="qh">+{{item.qh}}</span>
                                <span class="value"> {{item.phone}}</span>
                            </div>
                        </div>
                    </li>
                </ul>
                <div class="noRecord" v-else>
                    <img src="../../../assets/img/noRecordIcon.png" alt="">
                    <span>{{$t('noRecord')}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import $Tool from '../../../api/util';

  export default {
    name: 'InviteReward',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
          inviteCode: 'KHBJSK',
          inviteLink: 'http://www.windtalk.net/invite/khbjsk'
        },
        commission: { // 佣金
          coin: 19000.21331, // 金币
          dollar: 100  // 美元
        },
        inviteInfo: {
          inviteNum: 5, // 邀请人数
          orderNum: 3, // 下单人数
        },
        inviteHeadList: [
          {
            type: 'invite'
          },
          {
            type: 'order'
          }
        ],
        currentListType: 'invite',
        invitePersonList: [
          {
            nickname: 'Sky', // 用户昵称
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          },
          {
            nickname: 'Sky', // 用户昵称
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          },
          {
            nickname: 'Sky', // 用户昵称
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          }
        ],
        orderRewardList: [
          {
            commission: '8.68', // 佣金
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          },
          {
            commission: '8.68', // 佣金
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          },
          {
            commission: '8.68', // 佣金
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          },
          {
            commission: '8.68', // 佣金
            time: '2019.6.30 12:12:45', // 时间
            phone: '15235547895', // 手机号
            qh: '86' // 区号
          }
        ]
      }
    },
    methods: {
      selectListType(type) {
        if (this.currentListType !== type) {
          this.currentListType = type;
        }
      },
      onCopySuccess(e) {
        $Tool.prompt(this.$t('Prompt.hint.copy.success'))
        // console.log('复制成功:',e);
      },
      onCopyError(e) {
        $Tool.prompt(this.$t('Prompt.hint.copy.fail'))
        // console.log('复制失败:',e);
      },
      getCommission() {
        this.$ajax('/windmine-assets/commissionaccount', {}, res => {
          console.log('res', res.data);
          if (res.data.success == true) {
            this.commission.coin = res.data.data.coin;
            this.commission.dollar = res.data.data.dollar;
          }
        }, 'get');
      }
    },
    mounted() {

    }
  };
</script>

<style lang="less" scoped>
