        font-family: PingFangSC;
        font-weight: 400;
        .inviteRewardC {
            margin: 0 auto;
            padding: 0 0.1rem 0.2rem;
            width: 7.10rem;
            /*min-height: 8.7rem;*/
            background: rgba(255,255,255,1);
            box-shadow: 0 0.06rem 0.30rem 0 rgba(0,0,0,0.02);
            border-radius: 0.10rem;
            .inviteHead {
                height: 1.2rem;
                border-bottom: 1px solid rgba(243, 243, 243, 1);
                display: flex;
