                    }
                }
            }
            .inviteCodeC {
                height: 1.78rem;
                border-bottom: 1px solid rgba(243, 243, 243, 1);
                .title {
                    margin-left: 0.14rem;
                    display: flex;
                    align-items: center;
                    height: 0.74rem;
                    font-size: 0.28rem;
                    color: rgba(151,155,162,1);
                }
                .content {
                    margin-left: 0.08rem;
                    display: flex;
                    align-items: center;
                    font-size: 0.28rem;
                    color: rgba(0,0,0,1);
                    input {
                        width: 5.76rem;
                        height: 0.80rem;
                        background: rgba(250,249,249,1);
                        border-radius: 0.04rem;
                        border: 1px solid rgba(235,235,235,1);
                        text-indent: 0.2rem;
                        font-size: 0.28rem;
                    }
                    .copy {
                        margin-left: 0.4rem;
                    }
                }
            }
            .linkC {
                height: 1.78rem;
                border-bottom: 1px solid rgba(243, 243, 243, 1);
                .title {
                    margin-left: 0.14rem;
                    display: flex;
                    align-items: center;
                    height: 0.74rem;
                    font-size: 0.28rem;
                    color: rgba(151,155,162,1);
                }
                .content {
                    margin-left: 0.08rem;
                    display: flex;
                    align-items: center;
                    font-size: 0.28rem;
                    color: rgba(0,0,0,1);
                    input {
                        width: 5.76rem;
                        height: 0.80rem;
                        background: rgba(250,249,249,1);
                        border-radius: 0.04rem;
                        border: 1px solid rgba(235,235,235,1);
                        text-indent: 0.2rem;
                        font-size: 0.28rem;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .copy {
                        margin-left: 0.4rem;
                    }
                }
            }
            .rewardC {
                .title {
                    margin-left: 0.14rem;
                    display: flex;
