            padding: 0 0.1rem;
            width: 7.10rem;
            min-height: 5.34rem;
            background: rgba(255,255,255,1);
            border-radius: 0.10rem;
            .inviteHeadList {
                height: 1.12rem;
                display: flex;
                justify-content: center;
                border-bottom: 1px solid rgba(243, 243, 243, 1);
                & > li {
                    height: 0.5rem;
