(function (doc, win) {
    var systemFont = doc.documentElement.currentStyle ? doc.documentElement.currentStyle['fontSize'] : getComputedStyle(doc.documentElement,false)['fontSize'];
    var xs = parseFloat(systemFont) / 16;
    var docEl = doc.documentElement;
    var isIOS = navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    var dpr = isIOS ? Math.min(win.devicePixelRatio, 3) : 1;
    dpr = window.top === window.self ? dpr : 1;
    var resizeEvt = 'orientationchange' in window ?
        'orientationchange' :
        'resize';
    docEl.dataset.dpr = dpr;
    var recalc = function () {
        var width = docEl.clientWidth;
        // if (width / dpr > 750) {
        //     width = 750 * dpr;
        // }
        getDataset(docEl).width = width;
        getDataset(docEl).percent = 100 * (width / 750);
        docEl.style.fontSize = (100 * (width / 750)) / xs + 'px';
        // docEl.style.fontSize = (100 * (width / 750)) + 'px';
    };
    recalc();
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);
    function getDataset(ele) {
        if (ele.dataset) {
            return ele.dataset;
        } else {
            var attrs = ele.attributes,
                dataset = {},
                name,
                matchStr;
            for (var i = 0; i < attrs.length; i++) {
                matchStr = attrs[i].name.match(/^data-(.+)/);
                if (matchStr) {
                    name = matchStr[1].replace(/-([\da-z])/gi, function (all, varter) {
                        return varter.toUpperCase();
                    });
                    dataset[name] = attrs[i].value;
                }
            }
            return dataset;
        }
    }
})(document, window);



