<template>
    <div class="settingPage" id="settingPage">
        <Nav currentPage="Setting"></Nav>
        <div class="pageTitle">{{$t('Setting.title')}}</div>
        <ul class="headList">
            <li class="headItem" v-for="(item,index) in headList" :key="index" @click="selectItemType(item.type)">
                <span class="title" :class="{'act':currentItemType === item.type}">{{$t('Setting.head["'+item.type+'"]')}}</span>
                <span class="actDot" v-show="currentItemType === item.type"></span>
            </li>
        </ul>
        <!--<router-view></router-view>-->
        <component :is="currentItemType"></component>
        <!--<Footer></Footer>-->
    </div>
</template>

<script>
  import Address from '../../pages/H5/SettingPages/Address'
  import Remind from '../../pages/H5/SettingPages/Remind'
  import Safe from '../../pages/H5/SettingPages/Safe'
  export default {
    name: 'Setting',
    data() {
      return {
        currentItemType: 'Address',
        headList: [
          {
            type: 'Address',
            name: '收益地址'
          },
          {
            type: 'Remind',
            name: '到期提醒'
          },
          {
            type: 'Safe',
            name: '账户与安全'
          }
        ]
      }
    },
    methods: {
      init() {
        // this.$router.push({
        //   path: `/Setting/${this.currentItemType}`
        // })
      },
      selectItemType(type) {
        if (this.currentItemType !== type) {
          this.currentItemType = type;
          // this.$router.push({
          //   path: `/Setting/${this.currentItemType}`
          // })
        }
      }
    },
    mounted() {
      // this.init();
    },
    components: {
      Address,
      Remind,
      Safe
    }
  };
</script>

<style lang="less" scoped>
    .settingPage {
        background-color: rgba(243,244,248,1);
        .pageTitle {
            margin-top: 1px;
            height: 0.9rem;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #ffffff;
            font-size: 0.30rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(0,0,0,1);
        }
        .headList {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 1.22rem;
            & > li {
                width: 2rem;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                overflow: hidden;
                /*justify-content: center;*/
                .title {
                    margin-top: 0.38rem;
                    font-size: 0.28rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color: rgba(111,111,111,1);
                    text-align: center;
                }
                .title.act {
                    color:rgba(0,0,0,1);
                }
                .actDot {
                    margin-top: 0.06rem;
                    width: 0.12rem;
                    height: 0.12rem;
                    border-radius: 100%;
                    background: rgba(61,108,255,1);
                    box-shadow: 0 0.02rem 0.10rem 0 rgba(61,108,255,0.4);
                }
            }
            & > li:nth-child(1) {
                width: 5rem;
                /*.title {*/
                    /*width: 100%;*/
                    /*text-align: right;*/
                /*}*/
            }
            & > li:nth-child(3) {
                width: 5rem;
                /*.title {*/
                    /*width: 100%;*/
                    /*text-align: left;*/
                /*}*/
            }
        }
    }
</style>


