import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';

// TODO: 生成环境写的是完整路径，能访问。

const AJAXURL = process.env.NODE_ENV === 'development' ? '/api' : process.env.API_ROOT;

//k扩展的请求的方法
Vue.prototype.$ajax = function(url, data, callback, type = 'post') {
    let self = this;
    let options = {};
    if (type === 'get') {
        data = JSON.stringify(data) === '{}' ? '' : '?' + qs.stringify(data);
        options = {
            method: type,
            url: AJAXURL + url + data,
        };
    } else if(type === 'post') {
        options = {
            method: type,
            url: AJAXURL + url,
            data: data,
        };
    }
    // console.log(axios.defaults.headers);
    // axios.defaults.headers.common['Authorization'] = 'xxxxxx';
    axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
    axios(options).then(function(response) {
        if(callback) callback(response);
        // console.log('res',response);
    }).catch(function(err) {
        if(callback) callback(null,err);
        // console.log('error',err);
    });
}


Vue.prototype.$cacheAjax = function(url, data, callback, type = 'post') {
  if (type === 'get') {
    // only cache GET 200 response, callback 2 times
    let qstring = JSON.stringify(data) === '{}' ? '' : '?' + qs.stringify(data);
    // let getUrl = AJAXURL + url + qstring;
    let getUrl = AJAXURL + url;
    let cacheKey = 'publicKey' + '_' + getUrl;
    // let cacheData = $Tool.$state.getApiCache(cacheKey);
    let cacheData = tempVueObj.$store.state.apiCacheMap[cacheKey];
    console.log('cacheKey',cacheKey);
    console.log('cacheData',cacheData);
    //console.log("cacheAjax get", cacheKey, cacheData);
    if(cacheData && callback){
      callback(cacheData);
    }
    this.$ajax(url, data, function(response) {
      if(response){
        // $Tool.$state.setApiCache(cacheKey, response);
        tempVueObj.$store.commit('setApiCache',{'key':cacheKey,'data':response});
        console.log('tempVueObj.$store.state',tempVueObj.$store.state.apiCacheMap)
        //console.log("cacheAjax set", cacheKey, cacheData);
      }
      callback(response);
    }, type);
  }else{
    this.$ajax(url, data, callback, type);
  }
};


