import Vue from 'vue'
import VueI18n from 'vue-i18n'
import LangCN from './pack/zh';
import LangEN from './pack/en';
import store from '../store/'

Vue.use(VueI18n);
console.log('currentLanguage.type',store.state.currentLanguage.type);
const i18n = new VueI18n({
  locale: store.state.currentLanguage.type,    // 语言标识
  //this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    'CN': LangCN,   // 中文语言包
    'EN': LangEN    // 英文语言包
  }
});

export default i18n;


