                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.yieldInYear.title')}}</div>
                                <div class="itemValueC"><span class="value">{{item.yieldInYear}}</span><span class="unit">%</span></div>
                            </li>
                            <li class="calculateInDay">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.calculateInDay.title')}}</div>
                                <div class="itemValueC"><span class="value">${{item.calculateInDay}}</span><span class="unit">{{$t('Unit.T')}}</span></div>
                            </li>
                            <li class="electricInDay">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.electricInDay.title')}}</div>
                                <div class="itemValueC"><span class="value">${{item.electricInDay}}</span><span class="unit">{{$t('Unit.T')}}</span></div>
                            </li>
                        </ul>
                    </div>
                    <div class="introPartThree">
                        <div class="powerNumC">
                            <!--<div class="title">算力</div>-->
                            <div class="soldC">
                                <div class="title">{{$t('Calculate.product.sold.had')}}</div>
                                <div class="soldStatus">
                                    <div class="barC">
                                        <div class="barProgress" :style="{'width':Math.floor(item.soldRate * 100)+'%'}"></div>
                                    </div>
                                    <span class="soldRate">{{Math.floor(item.soldRate * 100)}}%</span>
                                </div>
                            </div>
                            <div class="handler">
                                <span class="sub" :class="{'disabled': !item.powerNum || item.powerNum <= item.minPowerNum}" @click="subPowerNum(item)">－</span>
                                <input type="number" v-model="item.powerNum" onclick="event.currentTarget.select()" onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))">
                                <span class="unit">TH/S</span>
                                <span class="add" @click="addPowerNum(item)" :class="{'disabled': item.powerNum >= item.maxPowerNum}" >＋</span>
