<template>
    <div class="coinOrderPage" id="coinOrderPage">
        <Nav></Nav>
        <div class="pageTitle">{{$t('CoinOrder.title')}}</div>
        <div class="orderC">
            <div class="headC">
                <div class="partOne">
                    <div class="coinType value">{{currentCoinItem.digCoinType}}</div>
                    <div class="title">{{$t('CoinOrder.head.cType')}}</div>
                </div>
                <hr>
                <div class="partTwo">
                    <div class="oreMachineType value">{{currentCoinItem.oreMachineType}}</div>
                    <div class="title">{{$t('CoinOrder.head.mType')}}</div>
                </div>
                <hr>
                <div class="partThree">
                    <div class="deadline value">{{currentCoinItem.deadline}}</div>
                    <div class="title">{{$t('CoinOrder.head.deadline')}}</div>
                </div>
            </div>
            <div class="contentC">
                <div class="powerNumC">
                    <div class="title">{{$t('CoinOrder.content.powerNum.title')}}</div>
                    <div class="handler">
                        <span class="sub" :class="{'disabled': !currentCoinItem.powerNum || currentCoinItem.powerNum <= currentCoinItem.minPowerNum}" @click="subPowerNum(currentCoinItem)">－</span>
                        <input type="number" v-model="currentCoinItem.powerNum" onclick="event.currentTarget.select()" onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))">
                        <span class="unit">TH/S</span>
                        <span class="add" @click="addPowerNum(currentCoinItem)" :class="{'disabled': currentCoinItem.powerNum >= currentCoinItem.maxPowerNum}" >＋</span>
                    </div>
                </div>
                <div class="calculateFeeC contentItem">
                    <div class="title">{{$t('CoinOrder.content.calculateFee.title')}}</div>
                    <div class="calculateFee value">$ {{currentCoinItem.powerToPrice}}</div>
                </div>
                <div class="electricDaysC contentItem">
                    <div class="title">{{$t('CoinOrder.content.electricDays.title')}}</div>
                    <div class="electricDays value">{{currentCoinItem.electricDays}}{{$t('Unit.day')}}</div>
                </div>
                <div class="electricFeeC contentItem">
                    <div class="title">{{$t('CoinOrder.content.electricFee.title')}}</div>
                    <div class="electricFee value">$ {{currentCoinItem.electricToPrice}}</div>
                </div>
                <div class="totalFeeC contentItem">
                    <div class="title">{{$t('CoinOrder.content.totalFee.title')}}</div>
                    <div class="totalFee value">$ {{currentCoinItem.totalPrice}}</div>
                </div>
                <div class="consumeCoinC contentItem">
                    <div class="title">{{$t('CoinOrder.content.consumeCoin.title')}}</div>
                    <div class="dollarToCoinIntro">($1 = {{windTalkCoinInfo.dollarToCoinRate}}{{$t('Unit.coin')}})</div>
                    <div class="consumeCoin value">{{currentCoinItem.totalPriceToCoin}}</div>
                </div>
                <div class="commissionExchangeC contentItem" v-if="currentCoinItem.currentCanUseCommission">
                    <div class="handleC">
                        <input type="checkbox" v-model="commissionUse">
                    </div>
                    <div class="title">{{$t('CoinOrder.content.commissionExchange.title')}}</div>
                    <div class="commissionCoin value">-{{currentCoinItem.currentCanUseCommission}}</div>
                </div>
                <div class="coinExchangeC contentItem">
                    <div class="title">{{$t('CoinOrder.content.coinExchange.title')}}</div>
                    <div class="deductionCoin value">{{currentCoinItem.currentDeductionCoin}}</div>
                </div>
                <div class="couponsC contentItem" v-if="false">
                    <div class="title">{{$t('CoinOrder.content.coupons.title')}}</div>
                    <div class="handler">
                        <span class="content">{{$t('CoinOrder.content.coupons.no')}}</span>
                        <span class="icon"></span>
                    </div>
                </div>
                <div class="agreementC contentItem">
                    <input type="checkbox" v-model="agreementFlag">
                    <span>{{$t('CoinOrder.content.agreement.one')}}</span>
                    <span class="agreementLink" @click="toAgreementPage">{{$t('CoinOrder.content.agreement.two')}}</span>
                </div>
                <div class="confirmC">
                    <div class="noPayHint" v-show="!confirmFlag">{{$t('CoinOrder.content.confirm.noPay')}}</div>
                    <button class="confirmBtn" :class="{'act':confirmFlag && agreementFlag}" @click="toPaymentPage">{{$t('CoinOrder.content.confirm.sure')}}</button>
                </div>
            </div>
            <div class="digStartTimes">{{$t('CoinOrder.content.digStartTime.title')}} {{currentCoinItem.digStartTimes}}</div>
        </div>
        <Attention></Attention>
        <Footer></Footer>
        <CouponsPrompt v-show="couponsPromptFlag"></CouponsPrompt>
    </div>
</template>

<script>
  import $Tool from '../../api/util';
  import CouponsPrompt from '../../components/CouponsPrompt'
  export default {
    name: 'coinOrder',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1, // btc 余额
          btcTotalOutput: 0.5, // btc累计产出
          commission: { // 佣金
            coin: 19000.21331, // 金币
            dollar: 100  // 美元
          },
          inviteCode: 'kxsass', // 邀请码
          inviteLink: 'http://www.windtalk.net/invite/khbjsk', // 邀请链接
          inviteNum: 5, // 邀请人数
          orderNum: 3 // 下单人数
        },
        windTalkCoinInfo: {
          dollarToCoinRate: 10000.12345 // 单位美元等于多少金币
        },
        currentCoinItem: {
          deadline: 7, // 期限
          yieldInDay: 0.3834,  // 理论每天每T收益
          yieldInYear: 179.03,  // 理论年化收益率
          calculateInDay: 0.1329, // 每天每T租赁费
          electricInDay: 0.1249, // 每天每T电费
          electricDays: 7, // 电费缴纳天数
          calculateStepNum: 1, // 算力步进值
          minPowerNum: 1,     // 购买算力下限
          maxPowerNum: 100,  // 购买算力上限
          digStartTimes: '19.8.30 08:00',  // 挖矿预计开始时间
          digCoinType: 'Bitcoin', // 挖取币种
          oreMachineType: 'Antminer S9 (6)', // 机型
          maxDeductionRate: 1, // 最大抵扣比率   待
          // 计算获取
          powerNum: 0, // 购买算力数量
          powerToPrice: 0, // 购买算力所需美元
          electricToPrice: 0,  // 购买电费所需美元
          totalPrice: 0, // 总费用
          totalPriceToCoin: 0, // 总费用折合成金币
          currentDeductionCoin: 0, // 当前所需花费金币
          currentCanUseCommission: 0 // 当前佣金抵扣
        },
        commissionUse: true,
        agreementFlag: true,
        couponsPromptFlag: false,
        confirmFlag: false,
      }
    },
    methods: {
      subPowerNum(item) {
        if (item.powerNum <= item.minPowerNum) return false;
        item.powerNum = $Tool.$sub(item.powerNum,item.calculateStepNum);
      },
      addPowerNum(item) {
        if (item.powerNum >= item.maxPowerNum) return false;
        item.powerNum = $Tool.$add(item.powerNum,item.calculateStepNum);
      },
      getCurrentCoinItem() {
        // 获取算力费用、电费、总计费用
        if (localStorage.getItem('currentCoinItem')) {
          this.currentCoinItem = Object.assign(this.currentCoinItem,JSON.parse(localStorage.getItem('currentCoinItem')));
        }
      },
      // TODO: 重置抵扣金币
      resetDeductionCoin() {
        this.currentCoinItem.totalPriceToCoin = $Tool.$mul(this.currentCoinItem.totalPrice, this.windTalkCoinInfo.dollarToCoinRate).toFixed(5);
        if (Number(this.currentCoinItem.totalPriceToCoin) >= Number(this.userInfo.commission.coin)) {
          this.currentCoinItem.currentCanUseCommission = this.userInfo.commission.coin;
        }else {
          this.currentCoinItem.currentCanUseCommission = this.currentCoinItem.totalPriceToCoin;
        }
        if (this.commissionUse) {
          this.currentCoinItem.currentDeductionCoin = $Tool.$sub(this.currentCoinItem.totalPriceToCoin,this.currentCoinItem.currentCanUseCommission);
          if (this.currentCoinItem.currentDeductionCoin > 0) {
            this.currentCoinItem.currentDeductionCoin = this.currentCoinItem.currentDeductionCoin.toFixed(5);
          }
        }else {
          this.currentCoinItem.currentDeductionCoin = this.currentCoinItem.totalPriceToCoin;
        }
        if (Number(this.currentCoinItem.currentDeductionCoin) >= Number(this.userInfo.coinBalance)) {
          this.confirmFlag = false;
        }else {
          this.confirmFlag = true;
        }
      },
      toAgreementPage() {

      },
      toPaymentPage() {
        if (!this.confirmFlag || !this.agreementFlag) return false;
        confirm('金币支付,暂时没有下级页面');
        // this.$router.push({
        //   path: '/Payment'
        // })
      }
    },
    mounted() {
      this.getCurrentCoinItem();
    },
    components: {
      CouponsPrompt
    },
    watch: {
      'currentCoinItem.powerNum': {
        handler: function (val,oldVal) {
          let item = this.currentCoinItem;
          if (!item.powerNum || item.powerNum <= item.minPowerNum) {
            item.powerNum = item.minPowerNum;
          }
          if (item.powerNum >= item.maxPowerNum) {
            item.powerNum = item.maxPowerNum;
          }
          // 公式: 合约天数 * (单位每天每T算力费 * 购买T数 + 每天电费)
          // 公式: 总算力费 + 总电费 ==>  合约天数 * (单位每天每T算力费 * 购买T数)  + 合约天数 * 每天电费
          item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline,$Tool.$mul(item.calculateInDay,item.powerNum||0)));
          item.electricToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.electricInDay,item.powerNum||0)));
          item.totalPrice = $Tool.$add(item.powerToPrice,item.electricToPrice);
          this.resetDeductionCoin();
        },
        deep: true
      },
      'commissionUse': {
        handler: function () {
          this.resetDeductionCoin();
        }
      }
    },
    created() {
      // TODO: 先校验是否登入 token

    }
  };
</script>

