            color: rgba(0,0,0,1);
        }
        .orderC {
            margin: 0 auto;
            width: 7.3rem;
            /*height: 11.92rem;*/
            background-color: rgba(255,255,255,1);
            box-shadow: 0 0.06rem 0.30rem 0 rgba(0,0,0,0.02);
            border-radius: 0.10rem;
            .headC {
                margin: 0 auto;
