                    }
                    .title {
                        font-size: 0.26rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(170,182,200,1);
                    }
                }
                & > hr {
                    width: 1px;
                    height: 0.63rem;
                    background-color: rgba(225, 225, 225, 1);
                    border: 0;
                }
                .partOne {
                    width: 2.0rem;
                }
                .partTwo {
                    width: 3.1rem;
                }
                .partThree {
                    width: 2.0rem;
                }
            }
            .contentC {
                margin: 0 auto;
                width: 7.1rem;
                .powerNumC {
                    height: 1.19rem;
                    border-bottom: 1px solid rgba(243, 243, 243, 1);
                    display: flex;
                    align-items: center;
                    .title {
                        margin-left: 0.14rem;
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
