<template>
    <div class="remindPage">
        <ul class="remindList">
            <li  v-for="(item,index) in remindList" :key="index">
                <div class="text">{{$store.state.currentLanguage.type === 'CN' ? $t('Setting.remind.text.one["'+item.type+'"]'): $t('Setting.remind.text.one') }}{{item.day}}{{$store.state.currentLanguage.type === 'CN' ?  $t('Setting.remind.text.two') : $t('Setting.remind.text.two["'+item.type+'"]')}}</div>
                <div class="swicthC" :class="{'act': item.status === '1'}" @click="toggleItemStatus(item)">
                    <div class="dot" :class="{'act': item.status === '1'}" ></div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
  export default {
    name: 'Remind',
    data() {
      return {
        remindList: [
          {
            type: '0', // 类型 0 套餐  1 电费
            day: '3', // 到期前多少天提醒
            status: '0', // 状态 0 关闭  1 开启
            // 重置文本
            text: '套餐到期前7天提醒我',
          },
          {
            type: '0', // 类型 0 套餐   1 电费
            day: '7', // 到期前多少天提醒
            status: '1', // 状态 0 关闭  1 开启
            text: '套餐到期前7天提醒我',
          },
          {
            type: '1', // 类型 0 套餐   1 电费
            day: '2', // 到期前多少天提醒
            status: '0', // 状态 0 关闭  1 开启
            text: '套餐到期前7天提醒我',
          },
          {
            type: '1', // 类型 0 套餐   1 电费
            day: '5', // 到期前多少天提醒
            status: '1', // 状态 0 关闭  1 开启
            text: '套餐到期前7天提醒我'
          }
        ]
      }
    },
    methods: {
      toggleItemStatus(item) {
        item.status = item.status === '1' ? '0' : '1';
      },
    },
    mounted() {

    }
  };
</script>

<style lang="less" scoped>
    .remindPage {
        margin: 0.2rem auto 0.2rem;
        width: 7.30rem;
        min-height: 7rem;
        background: rgba(255,255,255,1);
        box-shadow: 0 0.06rem 0.30rem 0 rgba(0,0,0,0.02);
        border-radius: 0.10rem;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        .remindList {
            padding: 0 0.1rem;
            & > li {
                height: 1rem;
                display: flex;
                align-items: center;
                border-bottom: 1px solid rgba(243, 243, 243, 1);
                .text {
                    width: 5rem;
                    margin-left: 0.14rem;
                    font-size: 0.28rem;
                    color: rgba(0,0,0,1);
                }
                .swicthC {
                    margin-left: auto;
                    margin-right: 0.28rem;
                    /*width: 48px;*/
                    /*height: 28px;*/
                    width: 0.96rem;
                    height: 0.54rem;
                    background: rgba(221,221,221,1);
                    border-radius: 0.27rem;
                    display: flex;
                    align-items: center;
                    transition: background 200ms;
                    .dot {
                        /*width: 25px;*/
                        /*height: 25px;*/
                        margin-left: 0.02rem;
                        width: 0.50rem;
                        height: 0.50rem;
                        border-radius: 100%;
                        background: rgba(255,255,255,1);
                        box-shadow: 0 0.05rem 0.02rem 0 rgba(0,0,0,0.05);
                        transition: margin-left 200ms;
                    }
                    .dot.act {
                        margin-left: 0.43rem
                    }
                }
                .swicthC.act {
                    background: rgba(68, 219, 94, 1);
                }
            }
        }
    }
</style>


