<template>
    <div class="personalPage" id="personalPage">
        <Nav currentPage="Personal"></Nav>
        <div class="pageTitle">{{$t('Personal.title')}}</div>
        <ul class="headList">
            <li class="headItem" v-for="(item,index) in headList" :key="index" @click="selectItemType(item.type)">
                <span class="title" :class="{'act':currentItemType === item.type}">{{$t('Personal.head["'+item.type+'"]')}}</span>
                <span class="actDot" v-show="currentItemType === item.type"></span>
            </li>
        </ul>
        <!--<router-view></router-view>-->
        <!--<keep-alive>-->
            <!--<component :is="currentItemType"></component>-->
        <!--</keep-alive>-->
        <component :is="currentItemType"></component>
        <Footer></Footer>
    </div>
</template>

<script>
  import AssetManage from '../../pages/H5/PersonalPages/AssetManage'
  import InviteReward from '../../pages/H5/PersonalPages/InviteReward'
  import MyOrder from '../../pages/H5/PersonalPages/MyOrder'
  import Coupons from '../../pages/H5/PersonalPages/Coupons'
  export default {
    name: 'Personal',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
        },
        headList: [
          {
            type: 'AssetManage',
            name: '资产管理', // 临时利用 可用type获取
          },
          {
            type: 'InviteReward',
            name: '邀请奖励',
          },
          {
            type: 'MyOrder',
            name: '我的订单',
          }
          // {
          //   type: 'Coupons',
          //   name: '优惠券',
          // }
        ],
        currentItemType: 'AssetManage',
      }
    },
    methods: {
      selectItemType(type) {
        if (this.currentItemType !== type) {
          this.currentItemType = type;
          // this.$router.push({
          //   path: `/Personal/${this.currentItemType}`
          // })
        }
      },
      init() {
        // this.$router.push({
        //   path: `/Personal/${this.currentItemType}`
        // })
      }
    },
    components: {
      AssetManage,
      InviteReward,
      MyOrder,
      Coupons
    },
    mounted() {
      // this.init();
    }
  };
</script>

<style lang="less" scoped>
    #personalPage {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
        background-color: rgba(243,244,248,1);
        /*display: flex;*/
        /*flex-direction: column;*/
        /*justify-content: center;*/
        /*-webkit-overflow-scrolling: touch;*/
        .pageTitle {
            margin-top: 1px;
            height: 0.9rem;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #ffffff;
            font-size: 0.30rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(0,0,0,1);
        }
        .headList {
            display: flex;
            align-items: center;
            justify-content: space-around;
            height: 1.22rem;
            & > li {
                /*width: 33%;*/
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                overflow: hidden;
                /*justify-content: center;*/
                .title {
                    margin-top: 0.38rem;
                    font-size: 0.28rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color: rgba(111,111,111,1);
                }
                .title.act {
                    color:rgba(0,0,0,1);
                }
                .actDot {
                    margin-top: 0.06rem;
                    width: 0.12rem;
                    height: 0.12rem;
                    border-radius: 100%;
                    background: rgba(61,108,255,1);
                    box-shadow: 0 0.02rem 0.10rem 0 rgba(61,108,255,0.4);
                }
            }
        }
    }
</style>


