            name: '生效中'
          },
          {
            type: 'complete',
            name: '已完成'
          }
        ]
      }
    },
    methods: {
      selectOutputType(type) {
        if (this.currentOutputType !== type) {
          this.currentOutputType = type;
        }
      },
      selectSingleType(type) {
        if (this.currentSingleType !== type) {
          this.currentSingleType = type;
        }
        // this.getSingleOutputList(); 重置原始状态 如果需要打开
      },
      toggleSingleDetail(item) {
        item.toggleDetail = !item.toggleDetail;
        console.log('this.singleOutputList',this.singleOutputList);
        console.log('this.singleOutputShowList',this.singleOutputShowList);
      },
      getSingleOutputList() {
        // this.singleOutputList 保留原始不变
        this.singleOutputSet.all = $Tool.deepCopy(this.singleOutputList);
        this.singleOutputSet.effect = $Tool.deepCopy(this.singleOutputList).filter(((value, index, array) => {
          return value.status === '1';
        }));
        this.singleOutputSet.complete = $Tool.deepCopy(this.singleOutputList).filter(((value, index, array) => {
          return value.status === '0';
        }));
      },
      toNextPage(page) {
        this.$router.push({
          path: `/AssetManage/${page}`
        })
      }
    },
    mounted() {
      this.getSingleOutputList();
    }
  };
