<template>
    <div class="calculateProductPage">
        <!--TODO: 共享算力计划-->
        <div class="calculateArea">
            <div class="head">
                <span class="title">{{$t('Calculate.cArea.title')}}</span>
            </div>
            <ul class="productList">
                <li class="productItem" v-for="(item,index) in realProductList" :key="index">
                    <div class="introPartOne">
                        <div class="deadline">
                            <div class="daysNum">{{item.deadline}}{{$t('Unit.day')}}</div>
                            <div class="title">{{$t('Calculate.product.deadline.title')}}</div>
                        </div>
                        <div class="yieldInDay">
                            <div class="yieldValue">$ {{item.yieldInDay}} {{$t('Unit.T')}}</div>
                            <div class="title">{{$t('Calculate.product.yieldInDay.title')}}</div>
                        </div>
                    </div>
                    <div class="introPartTwo">
                        <ul class="partTwoList">
                            <li class="paybackPeriod">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.paybackPeriod.title')}}</div>
                                <div class="itemValueC"><span class="value">{{item.paybackPeriod}}</span><span class="unit">{{$t('Unit.day')}}</span></div>
                            </li>

                            <li class="yieldInYear">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.yieldInYear.title')}}</div>
                                <div class="itemValueC"><span class="value">{{item.yieldInYear}}</span><span class="unit">%</span></div>
                            </li>
                            <li class="calculateInDay">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.calculateInDay.title')}}</div>
                                <div class="itemValueC"><span class="value">${{item.calculateInDay}}</span><span class="unit">{{$t('Unit.T')}}</span></div>
                            </li>
                            <li class="electricInDay">
                                <div class="itemIcon"></div>
                                <div class="itemName">{{$t('Calculate.product.electricInDay.title')}}</div>
                                <div class="itemValueC"><span class="value">${{item.electricInDay}}</span><span class="unit">{{$t('Unit.T')}}</span></div>
                            </li>
                        </ul>
                    </div>
                    <div class="introPartThree">
                        <div class="powerNumC">
                            <div class="soldC">
                                <div class="title">{{$t('Calculate.product.sold.had')}}</div>
                                <div class="soldStatus">
                                    <div class="barC">
                                        <div class="barProgress" :style="{'width':Math.floor(item.soldRate * 100)+'%'}"></div>
                                    </div>
                                    <span class="soldRate">{{Math.floor(item.soldRate * 100)}}%</span>
                                </div>
                            </div>
                            <div class="handler">
                                <span class="sub" :class="{'disabled': !item.powerNum || item.powerNum <= item.minPowerNum}" @click="subPowerNum(item)">－</span>
                                <input type="number" v-model="item.powerNum" onclick="event.currentTarget.select()" onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))">
                                <span class="unit">TH/S</span>
                                <span class="add" @click="addPowerNum(item)" :class="{'disabled': item.powerNum >= item.maxPowerNum}" >＋</span>
                            </div>
                        </div>
                        <div class="digTimesC" v-if="item.digStartTimes">
                            <div class="title">{{$t('Calculate.product.digTimes.title')}}</div>
                            <div class="startTimes">{{item.digStartTimes}}</div>
                        </div>
                        <div class="chargeC">
                            <div class="needChargeC">
                                <div class="title">{{$t('Calculate.product.charge.calculate')}}</div>
                                <div class="coinValue">${{item.powerToPrice}}</div>
                            </div>
                            <button class="chargeBtn" :class="{'disabled': item.soldRate >= 1}" @click="toOrderPage('ACalculate',item)" >{{item.soldRate < 1 ? $t('Calculate.product.btn.had') : $t('Calculate.product.btn.com')}}</button>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'ACalculateProduct',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
        },
        windTalkCoinInfo: {
          dollarToCoinRate: 10000 // 单位美元等于多少金币
        },
        realProductList: [
          {
            id: '', // 产品id
            deadline: 120, // 期限
            paybackPeriod: 170, //理论回本时间
            yieldInDay: 0.3834,  // 理论每天每T收益
            yieldInYear: 179.03,  // 理论年化收益率
            calculateInDay: 0.1329, // 每天每T租赁费
            calculateStepNum: 1, // 算力步进值
            electricInDay: 0.1249, // 每天每T电费
            electricMinDays: 10, // 电费天数最小值，也是默认值
            electricStepNum: 10, // 电费步进值
            minPowerNum: 1,     // 购买算力下限
            maxPowerNum: 100,  // 购买算力上限
            digStartTimes: '',  // 挖矿预计开始时间
            digCoinType: 'Bitcoin', // 挖取币种
            oreMachineType: 'Antminer S9 (6)', // 机型
            soldRate: 0.5, // 已出售比率
            // 计算获取
            electricDays: 0, // 电费缴纳天数
            powerNum: 1, // 购买算力数量
            powerToPrice: 0, // 购买算力所需美元
            electricToPrice: 0,  // 购买电费所需美元
            totalPrice: 0 // 总费用
          },
          {
            id: '', // 产品id
            deadline: 120, // 期限
            paybackPeriod: 170,
            yieldInDay: 0.3834,  // 理论每天每T收益
            yieldInYear: 179.03,  // 理论年化收益率
            calculateInDay: 0.1329, // 每天每T租赁费
            calculateStepNum: 1, // 算力步进值
            electricInDay: 0.1249, // 每天每T电费
            electricMinDays: 10, // 电费天数最小值，也是默认值
            electricStepNum: 10, // 电费步进值
            minPowerNum: 0,     // 购买算力下限
            maxPowerNum: 0,  // 购买算力上限
            digStartTimes: '19.8.30 08:00',  // 挖矿预计开始时间
            digCoinType: 'Bitcoin', // 挖取币种
            oreMachineType: 'Antminer S9 (6)', // 机型
            soldRate: 1, // 已出售比率
            // 计算获取
            electricDays: 0, // 电费缴纳天数
            powerNum: 1, // 购买算力数量
            powerToPrice: 0, // 购买算力所需美元
            electricToPrice: 0,  // 购买电费所需美元
            totalPrice: 0 // 总费用
          }
        ],
      }
    },
    methods: {
      subPowerNum(item) {
        if (item.powerNum <= item.minPowerNum) return false;
        item.powerNum = $Tool.$sub(item.powerNum,item.calculateStepNum);
      },
      addPowerNum(item) {
        if (item.powerNum >= item.maxPowerNum) return false;
        item.powerNum = $Tool.$add(item.powerNum,item.calculateStepNum);
      },
      toOrderPage(type,item) {
        if (item.soldRate >= 1) {return false;}
        localStorage.setItem(`current${type}Item`,JSON.stringify(item));
        this.$router.push({
          path:'/'+type+'Order'
        })
      },
      getRealProductList() {
        // 重置算力租赁费
        this.realProductList.forEach(((item, index, array) => {
          item.powerNum = item.minPowerNum;
          item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.calculateInDay,item.powerNum||0)));
        }))
      }
    },
    mounted() {
      this.getRealProductList();
    },
    watch: {
      // TODO: 每次改变，会循环两次。优化可以考虑镜像数组
      realProductList: {
        handler: function (arr, oldArr) {
          arr.forEach(item => {
            if (!item.powerNum || item.powerNum < item.minPowerNum) {
              item.powerNum = item.minPowerNum;
            }
            if (item.powerNum > item.maxPowerNum) {
              item.powerNum = item.maxPowerNum;
            }
            // 公式: 合约天数 * (单位每天每T算力费 * 购买T数)
            item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline, $Tool.$mul(item.calculateInDay,item.powerNum||0)));
          })
        },
        deep: true
      }
    }
  };
</script>

