<template>
    <div class="navPage">
        <div class="navC">
            <div class="langChange" @click="toggleLangList">
                <span class="langType">{{currentLanguage.name}}</span>
                <span class="downIcon" v-show="!langChangeToggle"></span>
                <span class="upIcon" v-show="langChangeToggle"></span>
                <ul class="langList"  v-show="langChangeToggle">
                    <li class="langItem" v-for="(item,index) in langTypeList" :key="index" :class="{'act':currentLanguage.type === item.type}" @click="langTypeChange(item)">{{item.name}}</li>
                </ul>
            </div>
            <div class="title">{{$t('title')}}</div>
            <div class="menu" @click="showMenu">
                <span class="menuIcon"></span>
            </div>
        </div>
        <Menu v-show="menuToggle" @closeMenu="closeMenu" :currentPage="currentPage"></Menu>
    </div>
</template>

<script>
  export default {
    name: 'Nav',
    data() {
      return {
        currentLanguage: {
          type: '',
          name: ''
        },
        langChangeToggle: false,
        langTypeList: [
          {
            type: 'CN',
            name: '中文'
          },
          {
            type: 'EN',
            name: 'English'
          },
        ],
        menuToggle: false
      }
    },
    methods: {
      toggleLangList() {
        this.langChangeToggle = !this.langChangeToggle;
      },
      langTypeChange(item) {
        Object.assign(this.currentLanguage, item);
        this.$store.commit('setCurrentLanguage', item);
        this.$i18n.locale = this.currentLanguage.type;
      },
      getCurrentLangType() {
        // TODO: 根据浏览器终端识别
        Object.assign(this.currentLanguage, this.$store.state.currentLanguage);
      },
      showMenu() {
        this.menuToggle = true;
      },
      closeMenu() {
        this.menuToggle = false;
      }
    },
    mounted() {
      console.log('切换了页面');
      console.log('currentPage', this.currentPage);
      this.currentPageT = this.currentPage;
      this.getCurrentLangType();
    },
    props: {
      currentPage: {
        type: String
      }
    }
  };
</script>

<style lang="less" scoped>
    .navC {
        position: relative;
        width: 100%;
        height: 0.98rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #ffffff;
        & > div {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .langChange {
            position: absolute;
            top: 0;
            left: 0.3rem;
            z-index: 1;

            .langType {
                height: 0.6rem;
                font-size: 0.28rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color: rgba(0,0,0,1);
                display: flex;
                align-items: center;
                /*background-color: cyan;*/
                /*width: 1.2rem;*/
            }
            .downIcon {
                margin-left: 0.2rem;
                width: 0.14rem;
                height: 0.09rem;
                background: url("../assets/img/downIcon.png") no-repeat center center;
                background-size: cover;
            }
            .upIcon {
                margin-left: 0.2rem;
                width: 0.10rem;
                height: 0.15rem;
                background: url("../assets/img/upIcon.png") no-repeat center center;
                background-size: cover;
            }
            .langList {
                position: absolute;
                left: 0;
                top: 0.8rem;
                z-index: 1;
                width: 1.80rem;
                background-color: rgba(247,247,247,1);
                box-shadow: 0 0.04rem 0.24rem 0 rgba(0,0,0,0.15);
                border-radius: 0.08rem;
                border: 1px solid rgba(212,212,212,1);
                & > li {
                    margin: 0.25rem 0;
                    font-size: 0.28rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color:rgba(65,65,65,1);
                    text-align: center;
                }
                & > li.act {
                    color: #3D6CFF;

                }
            }
        }
        .title {
            font-size: 0.34rem;
            font-family: Helvetica-Bold;
            font-weight: bold;
            color: rgba(0,0,0,1);
        }
        .menu {
            position: absolute;
            top: 0;
            right: 0.3rem;
            z-index: 1;
            .menuIcon {
                width: 0.4rem;
                height: 0.26rem;
                background: url("../assets/img/menu_Icon.png") no-repeat center center;
                background-size: cover;
            }
        }
    }
</style>


