<template>
    <div class="calculateOrderPage" id="calculateOrderPage">
        <div class="pageTitle">{{$t('CalculateOrder.title')}}</div>
        <div class="orderC">
            <div class="headC">
                <div class="partOne">
                    <div class="coinType value">{{currentCalculateItem.digCoinType}}</div>
                    <div class="title">{{$t('CalculateOrder.head.cType')}}</div>
                </div>
                <hr>
                <div class="partTwo">
                    <div class="oreMachineType value">{{currentCalculateItem.oreMachineType}}</div>
                    <div class="title">{{$t('CalculateOrder.head.mType')}}</div>
                </div>
                <hr>
                <div class="partThree">
                    <div class="deadline value">{{currentCalculateItem.deadline}}</div>
                    <div class="title">{{$t('CalculateOrder.head.deadline')}}</div>
                </div>
            </div>
            <div class="contentC">
                <div class="powerNumC">
                    <div class="title">{{$t('CalculateOrder.content.powerNum.title')}}</div>
                    <div class="handler">
                        <span class="sub" :class="{'disabled': !currentCalculateItem.powerNum || currentCalculateItem.powerNum <= currentCalculateItem.minPowerNum}" @click="subPowerNum(currentCalculateItem)">－</span>
                        <input type="number" v-model="currentCalculateItem.powerNum" onclick="event.currentTarget.select()" onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))">
                        <span class="unit">TH/S</span>
                        <span class="add" @click="addPowerNum(currentCalculateItem)" :class="{'disabled': currentCalculateItem.powerNum >= currentCalculateItem.maxPowerNum}" >＋</span>
                    </div>
                </div>
                <div class="calculateFeeC contentItem">
                    <div class="title">{{$t('CalculateOrder.content.calculateFee.title')}}</div>
                    <div class="calculateFee value">$ {{currentCalculateItem.powerToPrice}}</div>
                </div>
                <div class="electricDaysC contentItem">
                    <div class="title">{{$t('CalculateOrder.content.electricDays.title')}}</div>
                    <div class="handler">
                        <span class="sub" :class="{'disabled': currentCalculateItem.electricDays <= currentCalculateItem.electricMinDays}" @click="subElectricDays(currentCalculateItem)">－</span>
                        <input type="number" v-model="currentCalculateItem.electricDays" onclick="event.currentTarget.select()" onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))">
                        <span class="unit">{{$t('Unit.day')}}</span>
                        <span class="add" @click="addElectricDays(currentCalculateItem)" :class="{'disabled': currentCalculateItem.electricDays >= currentCalculateItem.deadline}">＋</span>
                    </div>
                </div>
                <div class="electricFeeC contentItem">
                    <div class="title">{{$t('CalculateOrder.content.electricFee.title')}}</div>
                    <div class="electricFee value">$ {{currentCalculateItem.electricToPrice}}</div>
                </div>
                <div class="totalFeeC contentItem">
                    <div class="title">{{$t('CalculateOrder.content.totalFee.title')}}</div>
                    <div class="totalFee value">$ {{currentCalculateItem.totalPrice}}</div>
                </div>
                <div class="commissionExchangeC contentItem">
                    <div class="handleC">
                        <input type="checkbox" v-model="commissionUse">
                    </div>
                    <div class="title">{{$t('CalculateOrder.content.commissionExchange.title')}}</div>
                    <div class="commissionCoin value">- $ {{currentCalculateItem.currentCanUseCommission}}</div>
                </div>
                <div class="couponsC contentItem" v-if="false">
                    <div class="title">{{$t('CalculateOrder.content.coupons.title')}}</div>
                    <div class="handler">
                        <span class="content">{{$t('CalculateOrder.content.coupons.no')}}</span>
                        <span class="icon"></span>
                    </div>
                </div>
                <div class="agreementC contentItem">
                    <input type="checkbox" v-model="agreementFlag">
                    <span>{{$t('CalculateOrder.content.agreement.one')}}</span>
                    <span class="agreementLink" @click="toAgreementPage">{{$t('CalculateOrder.content.agreement.two')}}</span>
                </div>
                <div class="confirmC">
                    <div class="payC">
                        <div class="title">{{$t('CalculateOrder.content.confirm.pay')}}</div>
                        <div class="payValue">$ {{payPrice}}</div>
                    </div>
                    <button class="confirmBtn" :class="{'act': agreementFlag }" @click="toPaymentPage">{{$t('CalculateOrder.content.confirm.sure')}}</button>
                </div>
            </div>
            <div class="digStartTimes">{{$t('CalculateOrder.content.digStartTime.title')}} {{currentCalculateItem.digStartTimes}}</div>
        </div>
        <Attention></Attention>
        <CouponsPrompt v-show="couponsPromptFlag"></CouponsPrompt>
    </div>
</template>

<script>
  import $Tool from '../../api/util';
  import CouponsPrompt from '../../components/CouponsPrompt'
  export default {
    name: 'calculateOrder',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
          commission: { // 佣金
            coin: 1000.21331, // 金币
            dollar: 100  // 美元
          }
        },
        currentCalculateItem: {
          deadline: 120, // 期限
          paybackPeriod: 170,
          yieldInDay: 0.3834,  // 理论每天每T收益
          yieldInYear: 179.03,  // 理论年化收益率
          calculateInDay: 0.1329, // 每天每T租赁费
          calculateStepNum: 1, // 算力步进值
          electricInDay: 0.1249, // 每天每T电费
          electricDays: 0, // 电费缴纳天数
          electricMinDays: 10, // 电费天数最小值，也是默认值
          electricStepNum: 10, // 电费步进值
          minPowerNum: 1,     // 购买算力下限
          maxPowerNum: 100,  // 购买算力上限
          digStartTimes: '',  // 挖矿预计开始时间
          digCoinType: 'Bitcoin', // 挖取币种
          oreMachineType: 'Antminer S9 (6)', // 机型
          soldRate: 0.5, // 已出售比率
          // 计算获取
          powerNum: 1, // 购买算力数量
          powerToPrice: 0, // 购买算力所需美元
          electricToPrice: 0,  // 购买电费所需美元
          totalPrice: 0, // 总费用
          currentCanUseCommission: 0 // 当前佣金抵扣
        },
        agreementFlag: true,
        couponsPromptFlag: false,
        commissionUse: true
      }
    },
    methods: {
      subElectricDays(item) {
        if (item.electricDays <= item.electricMinDays) return false;
        item.electricDays = $Tool.$sub(item.electricDays,item.electricStepNum);
      },
      addElectricDays(item) {
        if (item.electricDays >= item.deadline) return false;
        item.electricDays = $Tool.$add(item.electricDays,item.electricStepNum);
      },
      subPowerNum(item) {
        if (item.powerNum <= item.minPowerNum) return false;
        item.powerNum = $Tool.$sub(item.powerNum,item.calculateStepNum);
      },
      addPowerNum(item) {
        if (item.powerNum >= item.maxPowerNum) return false;
        item.powerNum = $Tool.$add(item.powerNum,item.calculateStepNum);
      },
      getCurrentCoinItem() {
        if (localStorage.getItem('currentACalculateItem')) {
          this.currentCalculateItem = Object.assign(this.currentCalculateItem,JSON.parse(localStorage.getItem('currentACalculateItem')));
          this.currentCalculateItem.electricDays = this.currentCalculateItem.electricMinDays;
        }
      },
      resetTotalPay() {
        let tatalPay = 0;
        if (Number(this.currentCalculateItem.totalPrice) >= Number(this.userInfo.commission.dollar)) {
          this.currentCalculateItem.currentCanUseCommission = this.userInfo.commission.dollar.toFixed(2);
        }else {
          this.currentCalculateItem.currentCanUseCommission = this.currentCalculateItem.totalPrice;
        }
        if (this.commissionUse) {
          tatalPay = $Tool.$sub(this.currentCalculateItem.totalPrice,this.currentCalculateItem.currentCanUseCommission).toFixed(2);
        }else {
          tatalPay = this.currentCalculateItem.totalPrice;
        }
        return tatalPay > 0 ? tatalPay : 0;
      },
      toAgreementPage() {

      },
      toPaymentPage() {
        if (!this.agreementFlag) return false;
        this.$router.push({
          path: '/APayment'
        })
      }
    },
    mounted() {
      this.getCurrentCoinItem();
      // console.log(this.payPrice)
    },
    components: {
      CouponsPrompt
    },
    computed: {
      payPrice() {
        let item = this.currentCalculateItem;
        item.totalPrice = $Tool.$add(item.powerToPrice,item.electricToPrice).toFixed(2);
        return this.resetTotalPay();
      }
    },
    watch: {
      'currentCalculateItem.powerNum': {
        handler: function (val, oldVal) {
          let item = this.currentCalculateItem;
          if (!item.powerNum || item.powerNum < item.minPowerNum) {
            item.powerNum = item.minPowerNum;
          }
          if (item.powerNum > item.maxPowerNum) {
            item.powerNum = item.maxPowerNum;
          }
          // 公式: 合约天数 * (单位每天每T算力费 * 购买T数 + 每天电费)
          // 公式: 总算力费 + 总电费 ==>  合约天数 * (单位每天每T算力费 * 购买T数)  + 合约天数 * 每天电费
          item.powerToPrice = $Tool.$ceil($Tool.$mul(item.deadline,$Tool.$mul(item.calculateInDay,item.powerNum||0)));
          item.electricToPrice = $Tool.$ceil($Tool.$mul(item.electricDays, $Tool.$mul(item.electricInDay,item.powerNum||0)));
        },
        deep: true
      },
      'currentCalculateItem.electricDays': {
        handler: function (val, oldVal) {
          let item = this.currentCalculateItem;
          if (!item.electricDays || item.electricDays < item.electricMinDays) {
            item.electricDays = item.electricMinDays;
          }
          if (item.electricDays > item.deadline) {
            item.electricDays = item.deadline;
          }
          item.electricToPrice = $Tool.$ceil($Tool.$mul(item.electricDays, $Tool.$mul(item.electricInDay,item.powerNum||0)));
        },
        deep: true
      }
    },
    created() {
      // TODO: 先校验是否登入 token

    }
  };
</script>

