<template>
    <div class="layer" @touchmove.prevent>
        <div class="promptC">
            <div class="head"></div>
            <div class="content">
                <div v-for="(item,index) in contentTextSet[currentType]" :key="index">{{$t('Prompt.common.text["'+currentType+'"]["'+item+'"]')}}</div>
            </div>
            <div class="footer">
                <button class="left" @click="handleLeftEvent">{{$t('Prompt.common.btn.left')}}</button>
                <button class="right act" @click="handleRightEvent">{{$t('Prompt.common.btn.right')}}</button>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'CommonPrompt',
    data() {
      return {
        // 中英文 采用方法重置
        currentType: 'signUp',
        contentTextSet: {
          signUp: ['0','1']
        }
      }
    },
    methods: {
      handleLeftEvent() {
        this.$emit('closeCommonPrompt');
      },
      handleRightEvent() {
        this.$emit('closeCommonPrompt');
        this.$router.push({
          path: './SignIn'
        })
      }
    },
    mounted() {

    }
  };
</script>

<style lang="less" scoped>
    .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 99;
        .promptC {
            margin: 0 auto;
            width: 5.70rem;
            min-height: 2.60rem;
            background: rgba(255,255,255,1);
            border-radius: 0.20rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .content {
                margin-top: auto;
                text-align: center;
                font-size: 0.32rem;
                color: rgba(0,0,0,1);
            }
            .footer {
                margin-top: 0.3rem;
                width: 100%;
                height: 1.0rem;
                border-top: 1px solid rgba(216,216,216,1);
                display: flex;
                align-items: center;
                justify-content: center;
                & > button {
                    width: 50%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 0.34rem;
                    color: rgba(0,0,0,1);
                    background-color: inherit;
                }
                & > button:first-child {
                    border-right: 1px solid rgba(216,216,216,1);
                }
                & > button.act {
                    color: rgba(61, 121, 255, 1);
                }

            }
        }

    }
</style>


