<template>
    <div class="signInPage" id="signInPage">
        <div class="head">
            <div class="title">{{$t('SignIn.title')}}</div>
        </div>
        <div class="signInContainer">
            <div class="hintText">{{$t('SignIn.hintText')}}</div>
            <ul class="formList">
                <li class="selected formItem" @click="toggleCountryFind">
                    <div class="countryCode">{{currentCountry[currentNameType]}} +{{currentCountry.qh}}</div>
                    <span class="downIcon" v-show="!countryFindToggle"></span>
                    <span class="upIcon" v-show="countryFindToggle"></span>
                    <SearchCountry v-show="countryFindToggle"  @selecteCountry="selecteCountry"></SearchCountry>
                </li>
                <li class="phoneNumber formItem">
                    <input type="number" :placeholder="$t('SignIn.phone_p')"  v-model="phoneNum" oninput="if(value.length > 15)value = value.slice(0, 15)">
                </li>
                <li class="password formItem" v-show="currentLoginMode === LOGINMODE.PASSWORD">
                    <input type="password" :placeholder="$t('SignIn.password_p')" v-model="password">
                </li>
                <li class="verifyCode formItem" v-show="currentLoginMode === LOGINMODE.CODE">
                    <input type="number" :placeholder="$t('SignIn.verifyCode_p')" v-model="verifyCode">
                    <span class="getVCode" @click="getVerifyCode" v-if="sendCodeCount === 0">{{$t('SignIn.getVCode')}}</span>
                    <span class="countDown" v-else>{{sendCodeCount}}s</span>
                </li>
            </ul>
            <div class="switchLogin" @click="switchLoginMode(LOGINMODE.CODE)" v-show="currentLoginMode === LOGINMODE.PASSWORD">{{$t('SignIn.switch.code')}}</div>
            <div class="switchLogin" @click="switchLoginMode(LOGINMODE.PASSWORD)" v-show="currentLoginMode === LOGINMODE.CODE">{{$t('SignIn.switch.password')}}</div>
            <div class="btnContainer">
                <button @click="signIn" :class="{'act':signInFlag}">{{$t('SignIn.button')}}</button>
            </div>
            <div class="otherChoose">
                <div class="signUp" @click="toSignUpPage">{{$t('SignIn.signUp')}}</div>
                <div class="forgetPassword" @click="toForgetPage" v-show="currentLoginMode === LOGINMODE.PASSWORD">{{$t('SignIn.forget')}}</div>
            </div>
            <div class="broadcast">{{$t('SignIn.broadcast')}}</div>
            <div class="downloadApp" @click="downloadApp">{{$t('SignIn.download')}}</div>
        </div>
    </div>
</template>

<script>
  import SearchCountry from '../../components/SearchCountry'
  import $Tool from '../../api/util';
  export default {
    name: 'SignIn', // 登入
    data() {
      return {
        countryFindToggle: false,
        currentLanguageType: 'CN',
        currentNameType: 'name_zh',  // name_zh    name_en
        currentCountry: {
          name_en: 'China',
          name_zh: '中国',
          countrysmall: '', // 缩写
          qh: '86', // 手机码
          countryCode: '', // 国家码
          chou: '' // 洲
        },
        currentLoginMode: 1, // 1 密码登入  2 短信验证码登入
        LOGINMODE: {
          PASSWORD: 1,
          CODE: 2
        },
        phoneMinLength: 3,
        phoneMaxLength: 15,
        sendCodeCount: 0,
        phoneNum: '',
        password: '',
        verifyCode: '',
        sendCodeStatus: false, // false 未发送  true 已发送
        sendCodeTimer: null,
        promptText: {
          phone: {
            CN: '手机号错误',
            EN: 'Phone number error',
          },
          codeTimeout: {
            CN: '验证码超时',
            EN: 'SMS code timeout',
          },
          codeError: {
            CN: '验证码错误',
            EN: 'SMS code error',
          },
          hadRegister: {
            CN: '用户已注册',
            EN: '',
          },
          countryCodeError: {
            CN: '国家码错误',
            EN: '',
          },
          unusual: {
            CN: '异常',
            EN: '',
          }
        },
        // signInFlag: false
      }
    },
    methods: {
      switchLoginMode(mode) {
        this.clearFormData();
        this.currentLoginMode = mode;
      },
      toSignUpPage() {
        this.$router.push({
          path: '/SignUp'
        })
      },
      toForgetPage() {
        this.$router.push({
          path: '/Forget'
        })
      },
      getCurrentNameType() {
        this.currentLanguageType = this.$store.state.currentLanguage.type;
        this.currentNameType = this.currentLanguageType === 'CN' ? 'name_zh' : 'name_en';
      },
      getCountryIP() {
        let url = '/community-user/ip';
        this.$ajax(url, {}, res => {
          if (res.data.success == true) {
            let _abbr;
            if (res.data.object) {
              _abbr = res.data.object.country_code;
            }else {
              _abbr = 'CN';
            }
            this.getCountryData(_abbr);
          }
        }, 'get');
      },
      getCountryData(abbr) {
        let url = '/community-user/findCountry';
        this.$ajax(url, {
          countrysmall: abbr,
        }, res => {
          console.log('res', res.data);
          if (res.data.success == true) {
            this.currentCountry = {...res.data.object[0]}
            // this.resetCountry(country);
          }
        }, 'get');
      },
      getVerifyCode() {
        if (this.verifyCode) {
          this.verifyCode = '';
        }
        if (this.currentCountry.countrysmall === 'CN') {
          if ($Tool.checkMobile(this.phoneNum)) {
            // TODO： 测试
            // this.sendCodeCount = 60;
            // this.createSendCodeTimer();
            // this.sendCode();
          } else {
            $Tool.prompt(this.promptText.phone[this.currentLanguageType]);
          }
        } else {
          if (this.phoneNum.length <= this.phoneMaxLength && this.phoneNum.length >= this.phoneMinLength) {
            // this.sendCode();
          } else {
            $Tool.prompt(this.promptText.phone[this.currentLanguageType]);
          }
        }
      },
      sendCode() {
        if (this.sendCodeStatus) { return false; }
        this.sendCodeStatus = true;
        this.$ajax('/community-user/sendCode', {
          qh: this.currentCountry.qh,
          phone: this.phoneNum,
          type: 0, // 类型 0:用户注册、1:登入、2:忘记密码
        }, res => {
          if (res && res.data.success == true) {
            // code 1 已经注册   code 0 没有注册 发送成功
            if (res.data.code === 1) {
              this.sendCodeStatus = false;
              // TODO:  老用户 弹框提示
              console.log('老用户');
            } else if (res.data.code === 0) {
              // TODO:  新用户
              this.sendCodeStatus = true; // --> 倒计时为0  false
              this.sendCodeCount = 60;
              this.createSendCodeTimer();
              console.log('新用户');
            }
          }else {
            this.sendCodeStatus = false;
          }
        }, 'get');
      },
      createSendCodeTimer() {
        if (this.sendCodeTimer) {
          clearInterval(this.sendCodeTimer);
          this.sendCodeTimer = null;
        }
        this.sendCodeTimer = setInterval(() => {
          this.sendCodeCount--;
          if (this.sendCodeCount <= 0) {
            clearInterval(this.sendCodeTimer);
            this.sendCodeTimer = null;
            this.sendCodeCount = 0;
            this.sendCodeStatus = false;
          }
        }, 1000);
      },
      selecteCountry(item) {
        this.countryFindToggle = false;
        this.currentCountry = {...item};
      },
      toggleCountryFind() {
        this.countryFindToggle = !this.countryFindToggle;
      },
      clearFormData() {
        if (this.sendCodeTimer) {
          clearInterval(this.sendCodeTimer);
          this.sendCodeTimer = null;
        }
        this.sendCodeCount = 0;
        this.phoneNum = '';
        this.password = '';
        this.verifyCode = '';
      },
      signIn() {
        if (!this.signInFlag) {return false}
        switch (this.currentLoginMode) {
          case this.LOGINMODE.PASSWORD:
            this.$ajax('windmine-user/login', {
              phone: this.phoneNum,
              qh: this.currentCountry.qh,
              pwd: this.password
            }, res => {
              if (res.data.success == true) {
                // TODO: 获取token
                // TODO: 跳转首页
              }
            }, 'post');
            break;
          case this.LOGINMODE.CODE:
            this.$ajax('/windmine-user/loginsms', {
              phone: this.phoneNum,
              qh: this.currentCountry.qh,
              sms: this.verifyCode
            }, res => {
              if (res.data.success == true) {
                // TODO: 获取token
                // TODO: 跳转首页
              }
            }, 'post');
            break;
          default:
            break;
        }
      },
      downloadApp() {

      }

    },
    mounted() {
      // TODO: 获取语言类型
      this.getCurrentNameType();
      // TODO: 获取所在地IP
      this.getCountryIP();
    },
    computed: {
      signInFlag: function () {
        // 先判断登录方式
        let _flag;
        switch (this.currentLoginMode) {
          case this.LOGINMODE.PASSWORD:
            if (this.currentCountry.countrysmall === 'CN') {
              _flag = $Tool.checkMobile(this.phoneNum) && $Tool.checkPassword(this.password);
            }else {
              _flag = (this.phoneNum.length >= this.phoneMinLength && this.phoneNum.length <= this.phoneMaxLength) && $Tool.checkPassword(this.password);
            }
            break;
          case this.LOGINMODE.CODE:
            if (this.currentCountry.countrysmall === 'CN') {
              _flag = $Tool.checkMobile(this.phoneNum) && $Tool.checkCode(this.verifyCode);
            }else {
              _flag = (this.phoneNum.length >= this.phoneMinLength && this.phoneNum.length <= this.phoneMaxLength) && $Tool.checkCode(this.verifyCode);
            }
            break;
          default:
            _flag = false;
            break;
        }
        console.log('flag',_flag);
        return _flag;

      }
    },
    components: {
      SearchCountry
    }
  };
</script>

<style lang="less" scoped>
    #signInPage {
        position: absolute;
        width: 100%;
        height: 100%;
        .head {
            height: 3.4rem;
            background-color: rgba(22,22,22,1);
            overflow: hidden;
            .title {
                margin-top: 1.1rem;
                text-align: center;
                font-size: 0.50rem;
                font-family: Helvetica-Bold;
                font-weight: bold;
                color: rgba(255,255,255,1);
            }
        }
        .signInContainer {
            /*position: absolute;*/
            /*left: 0;*/
            /*bottom: 0;*/
            margin-top: -0.7rem;
            width: 100%;
            height: 10.64rem;
            background: rgba(255,255,255,1);
            box-shadow: 0 -0.04rem 0.04rem 0 rgba(0,0,0,0.5);
            border-radius: 0.70rem 0.70rem 0 0;
            overflow: hidden;
            .hintText {
                margin: 0.44rem auto 0;
                width: 6.3rem;
                font-size: 0.50rem;
                font-family: PingFangSC-Semibold;
                font-weight: 600;
                color: rgba(0,0,0,1);
            }
            .formList {
                margin: 0.66rem auto 0;
                width: 6.3rem;
                & > li {
                    height: 0.90rem;
                    margin-bottom: 0.24rem;
                    /*padding-left: 0.5rem;*/
                    background-color: rgba(242,243,247,1);
                    border-radius: 0.45rem;
                    display: flex;
                    align-items: center;
                    & > input {
                        width: 3.80rem;
                        height: 0.42rem;
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        background-color: inherit;
                        margin-left: 0.5rem;
                        /*background-color: cyan;*/
                    }
                }
                .selected {
                    position: relative;
                    .countryCode {
                        width: 4.80rem;
                        height: 0.42rem;
                        /*background-color: white;*/
                        font-size: 0.30rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(103,103,103,1);
                        margin-left: 0.5rem;
                    }
                    .downIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.11rem solid transparent;
                        border-right: 0.11rem solid transparent;
                        border-top: 0.18rem solid rgba(188,188,188,1);
                        /*margin: 0 0.4rem 0 auto;*/
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .upIcon {
                        width: 0;
                        height: 0;
                        border-left: 0.18rem solid rgba(188,188,188,1);
                        border-top: 0.11rem solid transparent;
                        border-bottom: 0.11rem solid transparent;
                        margin: 0 0.4rem 0 auto;
                    }
                }
                .verifyCode {
                    .getVCode {
                        width: 1.40rem;
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                    .countDown {
                        font-size: 0.28rem;
                        font-family: PingFangSC-Regular;
                        font-weight: 400;
                        color: rgba(134,143,163,1);
                        margin-left: auto;
                        margin-right: 0.4rem;
                    }
                }
            }
            .switchLogin {
                margin: 0 auto;
                width: 6.3rem;
                font-size: 0.26rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color:rgba(86,110,163,1);
            }
            .btnContainer {
                margin: 0.50rem auto 0;
                width: 6.3rem;
                padding-bottom: 0.26rem;
                text-align: center;
                button {
                    /*display: flex;*/
                    /*align-items: center;*/
                    /*justify-content: center;*/
                    /*margin: 0 auto;*/
                    width: 6.30rem;
                    height: 0.96rem;
                    background-color: rgba(61,108,255,1);
                    border: 0;
                    box-shadow: 0 0.08rem 0.2rem -0.09rem rgba(61,121,255,0.5);
                    border-radius: 0.48rem;
                    font-size: 0.32rem;
                    font-family: HelveticaNeue;
                    color: rgba(255,255,255,1);
                    opacity: 0.5;
                }
                button.act {
                    opacity: 1;
                }
            }
            .otherChoose {
                margin: 0.26rem auto 0;
                width: 6.3rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-size: 0.26rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color: rgba(134,143,163,1);
            }
            .broadcast {
                margin: 0.66rem auto 0;
                padding-top: 0.22rem;
                width: 6.3rem;
                border-top: 1px solid rgba(242,243,247,1);
                text-align: center;
                font-size: 0.28rem;
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color: rgba(51,51,51,1);
            }
            .downloadApp {
                margin-top: 0.1rem;
                text-align: center;
                font-size: 0.28rem;
                font-family: PingFangSC-Medium;
                font-weight: 500;
                color: rgba(61,121,255,1);
            }
        }
        input {
            border: 0;
        }
    }
</style>


