<template>
    <div class="layer" @touchmove.prevent>
        <div class="promptC">
            <div class="head">{{$t('Prompt.password.head')}}</div>
            <div class="content">
                <div class="verifyCodeC">
                    <input type="number" class="verifyCode" v-model="verifyCode"
                           :placeholder="$t('Prompt.password.verifyCode_p')">
                    <span class="getVCode" @click="getVerifyCode" v-if="sendCodeCount === 0">{{$t('Prompt.password.getVCode')}}</span>
                    <span class="countDown" v-else>{{sendCodeCount}}s</span>
                </div>
                <div class="passwordC">
                    <input type="text" class="password" v-model="newPassword"
                           :placeholder="$t('Prompt.password.password_p')">
                </div>
            </div>
            <div class="footer">
                <button class="left" @click="handleLeftEvent">{{$t('Prompt.password.btn.left')}}</button>
                <button class="right act" @click="handleRightEvent">{{$t('Prompt.password.btn.right')}}</button>
            </div>
        </div>
    </div>
</template>

<script>
  import $Tool from '../api/util';

  export default {
    name: 'PasswordPrompt',
    data() {
      return {
        userInfo: {
          coinBalance: '121473.12345', // 金币余额
          phoneNumber: '15292092212', // 手机号
          qh: '86', // 区号
          btcBalance: 1,
          btcTotalOutput: 0.5, // btc累计产出
        },
        verifyCode: '',
        newPassword: '',
        sendCodeCount: 0,
        sendCodeStatus: false
      };
    },
    methods: {
      handleLeftEvent() {
        this.$emit('closePasswordPrompt')
      },
      handleRightEvent() {
        // TODO: 确认密码修改
        if (!this.verifyCode) {$Tool.prompt(this.$t('Prompt.hint.verifyCode')); return false;}
        if (!this.newPassword) {$Tool.prompt(this.$t('Prompt.hint.password')); return false;}
        // TODO: 调用密码修改接口
        this.$emit('closePasswordPrompt')
      },
      getVerifyCode() {
        if (this.sendCodeStatus) { return false; }
        this.sendCodeStatus = true;
        // TODO: 获取验证码接口
        // ...
      },
    },
    mounted() {

    }
  };
</script>

<style lang="less" scoped>
    .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 99;
        .promptC {
            margin: 0 auto;
            width: 6.90rem;
            height: 4.70rem;
            background: rgba(255, 255, 255, 1);
            border-radius: 0.20rem;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .head {
                height: 1.46rem;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 0.40rem;
                font-family: PingFangSC-Semibold;
                font-weight: 600;
                color: rgba(32, 47, 66, 1);

            }
            .content {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-bottom: 0.3rem;
                & > div {
                    border: 1px solid rgba(235, 235, 235, 1);
                    width: 6.20rem;
                    height: 0.80rem;
                    margin-bottom: 0.2rem;
                    padding-left: 0.2rem;
                    background: rgba(250, 249, 249, 1);
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    & > input {
                        width: 100%;
                        height: 100%;
                        background: inherit;
                        border-radius: 0.04rem;
                        font-size: 0.28rem;
                        color: #000000;
                    }
                }
                .verifyCodeC {
                    /*display: flex;*/
                    /*align-items: center;*/
                    & > input {
                        width: 60%;
                    }
                    & > span {
                        margin-left: auto;
                        margin-right: 0.3rem;
                        font-size: 0.28rem;
                        color: rgba(134,143,163,1);
                    }
                }
            }
            .footer {
                margin-top: auto;
                width: 100%;
                height: 1.0rem;
                border-top: 1px solid rgba(216, 216, 216, 1);
                display: flex;
                align-items: center;
                justify-content: center;
                & > button {
                    width: 50%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 0.34rem;
                    color: rgba(0, 0, 0, 1);
                    background-color: inherit;
                }
                & > button:first-child {
                    border-right: 1px solid rgba(216, 216, 216, 1)
                }
                & > button.act {
                    color: rgba(61, 121, 255, 1);
                }

            }
        }

    }
</style>


